import React, { useState } from 'react';
import { Link } from 'react-router-dom';

import { sendCatergory } from '../../../http/index';

export default function AdminProductCatergoryForm() {

    const [name, setName] = useState('');

    const [image, setImage] = useState('');

    const [des, setDes] = useState('');

    const[er,setEr]=useState(false);

    const [com, setCom] = useState({
        status: false,
        data: "",
    });


    async function Submit(e) {

        e.preventDefault();
       // console.log("button clicked");

        if (!name || !image || !des) {
           // console.log("validtion erro");

            setEr(true);
            return;

        }
        const mydata = new FormData();

        mydata.append("name", name.toLowerCase());
        mydata.append("des", des.toLowerCase());
        mydata.append("caterimage", image);

        try {
            const { data } = await sendCatergory(mydata);

           // console.log(data);

            if (data.pr) {

                //{pr,d:{name:"",_id}}
                setCom({
                    status: true,
                    data: data.d,
                })
            }

        } catch (error) {

           // console.log(error);
            setEr(true);

        }



    }
    return (
        <div>
            <p>welcome to product catergory form </p>


            {
                com.status ? <div>
                    <p style={{ color: "green" }}>item add sucessfully </p>
                    <p>{com.data._id}</p>
                    <p>{com.data.name}</p>
                    <Link to={'/admin/dashboard/catergory'}>
                        go catergory
                    </Link>
                </div> : <form>
                    <div>
                        {er&&<p style={{color:'red'}}>
                            all feilds are required 
                            </p>}
                    </div>
                    <label>
                        Enter Name of Catergory
                        <input type="text" value={name} onChange={(e) => {
                            setName(e.target.value);


                        }}></input>
                    </label><br></br>
                    <label>
                        Enter description of catergory
                        <input type="text" value={des} onChange={(e) => {

                            setDes(e.target.value);


                        }}></input>
                    </label><br></br>
                    <label>
                        Choose Image
                        <input type="file" onChange={(e) => {
                            setImage(e.target.files[0]);

                        }}></input>
                    </label><br></br>

                    <button onClick={Submit}>
                        Submit
                    </button>
                </form>
            }

        </div>
    )
}
