import { Typography, Container, Grid, Button } from '@material-ui/core';
import React, { useEffect, useState } from 'react';

import { useParams } from 'react-router-dom';
import moment from 'moment';
import { otherSingleOrder } from '../../../http/index';
import MyLoader from '../../../components/MyLoader/MyLoader';
import { useHistory } from 'react-router-dom';

export default function AotherSingleOrder() {
  const [ld, setld] = useState(true);
  const [md, setmd] = useState({ items: [], fullAddress: {}, pincodeId: {}, customerId: {}, py: {} });

  const { id } = useParams();


  const history = useHistory();

  function editorder() {

    history.push(`/admin/dashboard/other/orders/edit/data/${id}`);

  }

  useEffect(() => {

    (async () => {

      try {

        const { data } = await otherSingleOrder(id);

        //console.log(data);

        if (data.or) {

          setmd(data.result);
        }

      } catch (error) {

        //console.log(error);

      }

      return setld(false);

    })()
  }, [id]);

  return (
    <div>

      {
        ld ? <MyLoader></MyLoader> : <>
          <Container>
            <Typography variant='h6' style={{ margin: "32px 0px" }}>
              SingleOrder {id}
            </Typography>
          </Container>

          <Container style={{ marginBottom: "24px" }}>
            <Grid container spacing={3} justifyContent='space-between' alignItems='center'>
              <Grid item>
                <Typography variant='subtitle2'>
                  OrderId  {md._id}
                </Typography>
              </Grid>
              <Grid item>

                <Button onClick={editorder} color='secondary' variant='contained'>
                  Edit Order
                </Button>
              </Grid>
            </Grid>
          </Container>

          <Container>
            <Typography variant='h6' style={{ margin: "24px 0px" }}>
              Order Summary
            </Typography>
            <Grid container alignItems='center' justifyContent='space-between' style={{ textTransform: 'capitalize' }}>

              <Grid item >
                <Typography variant='subtitle2'>
                  total quanity {md.totalquantity}
                </Typography>
                <Typography variant='subtitle2'>
                  total amount of items  <i className="fa fa-inr" aria-hidden="true"></i> {md.totalamount}
                </Typography>
                <Typography variant='subtitle2'>
                  total delivery charge  <i className="fa fa-inr" aria-hidden="true"></i> {md.totaldeliverycharge}
                </Typography>
                <Typography variant='h6'>
                  total amount of order  <i className="fa fa-inr" aria-hidden="true"></i> {md.totalAmountofOrder}
                </Typography>
                <Typography style={{ color: "blue" }}>
                  order status  {md.status}
                </Typography>

                <Typography variant='subtitle2' style={{ marginBottom: "4px", }}>
                  Payment Type   {md.paymentType}
                </Typography>
                <Typography>
                  createdAt    {moment(md.createdAt).format("LLL")}
                </Typography>
                <Typography>
                  delivery date    {moment(md.deliverDate).format("LLL")}
                </Typography>

              </Grid>
              <Grid item >
                <Typography>
                  customer id  {md.customerId._id}
                </Typography>
                <Typography>
                  name  {md.name}
                </Typography>
                <Typography>
                  phone number  {md.phoneNumber}
                </Typography>
                <Typography>
                  delivery address  {md.address}
                </Typography>
                <Typography>
                  {md.fullAddress.nameOfCity} {" "} {md.fullAddress.stateOfCity} {" "} {md.fullAddress.countryOfCity}
                  {" "} {md.pincodeId.pincode}
                </Typography>
              </Grid>
            </Grid>
          </Container>

          <Container>
            <Typography variant='h6' style={{ margin: "24px 0px" }}>
              Order Items
            </Typography>
            {
              md.items.map((i) => {

                return (
                  <Grid key={i._id} container alignItems='center' justifyContent='space-between' style={{ textTransform: 'capitalize', marginBottom: "24px" }}>

                    <Grid item xs={6} sm={4} container justifyContent='center' alignItems='center'>

                      <Grid item >
                        <img src={i.productImage} alt={i.name} style={{ maxWidth: "100%", height: "150px" }}></img>
                      </Grid>

                    </Grid>
                    <Grid item xs sm>
                      <Typography variant='subtitle2'>
                        Product id {i.productId}
                      </Typography>
                      <Typography>
                        name  {i.name}
                      </Typography>
                      <Typography variant='subtitle2'>
                        Price of item  <i className="fa fa-inr" aria-hidden="true"></i>  {i.baseprice}
                      </Typography>
                      <Typography>
                        Total quanity    {i.quantity}
                      </Typography>
                      <Typography>
                        Total amount  <i className="fa fa-inr" aria-hidden="true"></i> {i.totalamount}
                      </Typography>
                      <Typography>
                        Total delivery charge  <i className="fa fa-inr" aria-hidden="true"></i>  {i.totaldelivery}
                      </Typography>
                      <Typography variant='h6'>
                        Total Amount of product  <i className="fa fa-inr" aria-hidden="true"></i>  {i.totalamount}
                      </Typography>
                    </Grid>
                  </Grid>
                )
              })
            }

          </Container>

        </>
      }


    </div>
  )
}
