import React, { useEffect, useState } from 'react';

import { sendProfile, getFullAddressForForm } from '../../http/index';

import { useDispatch } from 'react-redux';

import { setAuth } from '../../store/sellerSlice';
import { Box, Container, Grid, TextField, Typography, MenuItem, Button } from '@material-ui/core';

import MyLoader from '../../components/MyLoader/MyLoader';

export default function ActivateStep() {

    //console.log("activate componet");


    return (
        <StepBasic></StepBasic>
    )
}


function StepBasic() {

    const disptach = useDispatch();

    const [fullAdd, setFullAdd] = useState({
        city: [],
        state: [],
        pincode: [],
        country: '',
    });


    const [name, setName] = useState('');
    const [des, setDes] = useState('');

    const [catergory, setCatergory] = useState('');

    const [nameofbussiness, setNameOfBussiness] = useState('');

    const [gst, setGst] = useState("");
    const [shopimage, setShopImage] = useState('');


    const [address1, setAddress1] = useState('');
    const [address2, setAddress2] = useState("");
    let [city, setCity] = useState("");
    let [mystate, setMyState] = useState("");

    let [pincode, setPinCode] = useState("");

    const [er, setEr] = useState(false);

    const [ld, setld] = useState(true);


    async function submit(e) {

        e.preventDefault();

        //console.log("button clicked");

        // checking the validtion

        if (!pincode || !mystate || !city) {

            return setEr(true);
        }

        if (!name || !gst || !shopimage || !address1 || !address2 || !nameofbussiness) {

            //console.log("validition error");

            return setEr(true);
        }



        const mydata = new FormData();

        mydata.append("name", name.toLowerCase());
        mydata.append("des", des.toLowerCase());
        mydata.append("catergory", catergory.toLowerCase());
        mydata.append("nameofbussiness", nameofbussiness.toLowerCase());

        mydata.append('gst', gst.toUpperCase());
        mydata.append('shopimage', shopimage);
        mydata.append("address1", address1.toLowerCase());

        mydata.append('address2', address2.toLowerCase());

        mydata.append("city", city);

        mydata.append('state', mystate);

        mydata.append('country', fullAdd.country);
        mydata.append('pincode', parseInt(pincode));

        // console.log("everything is ok ");

        try {


            const { data } = await sendProfile(mydata);

            // console.log(data);

            if (data.auth) {

              return  disptach(setAuth({ seller: data.sellerDto }));

            }

        } catch (error) {

            //console.log(error);

           return setEr(true);
        }

    }

    useEffect(() => {

        //console.log("useeffect calling for get seller fulladdress form");

        (async () => {

            try {
                const { data } = await getFullAddressForForm();

                //console.log(data);

                if (data.or) {

                    setFullAdd(data.result);
                }

            } catch (error) {

                //console.log(error);

            }

            return setld(false);

        })()
    }, [])


    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>
                    <Container>

                        <Box textAlign={'center'} paddingTop={"24px"}>
                            <Typography variant='h6' style={{ marginBottom: '16px' }}>
                                Activate Your Profile
                            </Typography>

                            <Typography variant='h6' style={{ marginBottom: '24px' }}>
                                Enter Some Basic Details of Your Bussiness
                            </Typography>

                        </Box>

                        <Grid container justifyContent='center' style={{ textTransform: 'capitalize' }}>

                            <Grid item xs={12} sm={8}>
                                <form>
                                    {
                                        er && <Box marginBottom='16px' textAlign={'center'} >
                                            <Typography style={{ color: "red" }}>
                                                validition Error 
                                            </Typography>
                                        </Box>
                                    }

                                    <Box display={'flex'} marginBottom='16px' alignItems={'center'}>
                                        <Typography >
                                            Name of Supplier
                                        </Typography>
                                        <TextField value={name} variant='outlined' size='small' style={{ width: '100%' }} onChange={(e) => {
                                            setName(e.target.value);
                                        }}>

                                        </TextField>
                                    </Box>
                                    <Box display={'flex'} marginBottom='16px' alignItems={'center'}>
                                        <Typography >
                                            Name of description of Supplier
                                        </Typography>
                                        <TextField variant='outlined' size='small' type={'text'} style={{ width: '100%' }} value={des} onChange={(e) => {
                                            setDes(e.target.value);

                                        }}>

                                        </TextField>
                                    </Box>
                                    <Box display={'flex'} marginBottom='16px' alignItems={'center'}>
                                        <Typography >
                                            catergory of seller
                                        </Typography>
                                        <TextField variant='outlined' size='small' type={'text'} style={{ width: '100%' }} value={catergory} onChange={(e) => {
                                            setCatergory(e.target.value);
                                        }}>

                                        </TextField>
                                    </Box>

                                    <Box display={'flex'} marginBottom='16px' alignItems={'center'}>
                                        <Typography >
                                            Name of Bussiness
                                        </Typography>
                                        <TextField variant='outlined' size='small' type={'text'} style={{ width: '100%' }} value={nameofbussiness} onChange={(e) => {
                                            setNameOfBussiness(e.target.value);
                                        }}>

                                        </TextField>
                                    </Box>


                                    <Box display={'flex'} marginBottom='16px' alignItems={'center'}>
                                        <Typography >
                                            gst number of shop
                                        </Typography>
                                        <TextField variant='outlined' size='small' type={'text'} style={{ width: '100%', textTransform: 'uppercase' }} value={gst} onChange={(e) => {
                                            setGst(e.target.value);

                                        }}>

                                        </TextField>

                                    </Box>
                                    <Box display={'flex'} marginBottom='16px' alignItems={'center'}>
                                        <Typography >
                                            image of shop
                                        </Typography>
                                        <TextField variant='outlined' size='small' type={'file'} style={{ width: '100%' }} onChange={(e) => {


                                            setShopImage(e.target.files[0]);


                                        }} >

                                        </TextField>

                                    </Box>


                                    <Box display={'flex'} marginBottom='16px' alignItems={'center'}>
                                        <Typography >
                                            first address of shop
                                        </Typography>

                                        <TextField variant='outlined' size='small' type={'text'} style={{ width: '100%' }} value={address1} onChange={(e) => {
                                            setAddress1(e.target.value);

                                        }}>

                                        </TextField>

                                    </Box>

                                    <Box display={'flex'} marginBottom='16px' alignItems={'center'}>
                                        <Typography >
                                            second address of shop
                                        </Typography>

                                        <TextField variant='outlined' size='small' type={'text'} style={{ width: '100%' }} value={address2} onChange={(e) => {
                                            setAddress2(e.target.value);

                                        }}>

                                        </TextField>

                                    </Box>



                                    <Box marginBottom='16px' >
                                        <TextField variant='outlined' size='small' select label='select your city' style={{ width: '100%' }} value={city} onChange={(e) => {
                                            setCity(e.target.value);
                                        }}>
                                            {
                                                fullAdd.city.map((i) => {
                                                    return <MenuItem key={i} value={i}>{i}</MenuItem>
                                                })
                                            }
                                        </TextField>
                                    </Box>

                                    <Box marginBottom='16px' >
                                        <TextField select variant='outlined' size='small' label='select your state' style={{ width: '100%' }} value={mystate} onChange={(e) => {
                                            setMyState(e.target.value);
                                        }}>
                                            {
                                                fullAdd.state.map((i) => {
                                                    return <MenuItem key={i} value={i}>{i}</MenuItem>
                                                })
                                            }
                                        </TextField>
                                    </Box>
                                    <Box marginBottom='16px' >
                                        <TextField select variant='outlined' size='small' label='select your pincode' style={{ width: '100%' }} value={pincode} onChange={(e) => {
                                            setPinCode(e.target.value);
                                        }}>
                                            {
                                                fullAdd.pincode.map((i) => {
                                                    return <MenuItem key={i} value={i}>{i}</MenuItem>
                                                })
                                            }
                                        </TextField>
                                    </Box>

                                    <Box marginBottom='16px' >
                                        <Typography>
                                            country {fullAdd.country}
                                        </Typography>
                                    </Box>


                                    <Button onClick={submit} color='primary' variant='contained'>
                                        Submit
                                    </Button>

                                </form>
                            </Grid>
                        </Grid>



                    </Container>


                </>
            }


        </div>
    )
}


