import { Container, Typography } from '@material-ui/core';
import React, { useState, useEffect } from 'react'
import { useParams } from 'react-router-dom';

import { getSingleSubCatergory } from '../../../http/index';

export default function AdminSingleSubCatergory() {


  const [md, setmd] = useState({});

  const { id } = useParams();

  useEffect(() => {

    (async () => {

      try {
        const { data } = await getSingleSubCatergory(id);

        //console.log(data);

        if (data.or) {
          return setmd(data.result);

        }
      } catch (error) {
        
        console.log(error);
      }

    })()

  }, [id]);
  return (
    <div>

      <Container>
        <Typography>
          single sub catergory {id}
        </Typography>
        <Typography>
          name of subcatergory {md.name}
        </Typography>

      </Container>


    </div>
  )
}
