import { Container, Grid, Typography } from '@material-ui/core';
import React, { useEffect, useState } from 'react';

import { useParams } from 'react-router-dom';
import { Link } from 'react-router-dom';

import { getSingleBookingWorkforadmin } from '../../../http';

import moment from 'moment';
import MyLoader from '../../../components/MyLoader/MyLoader';

export default function SingleAdminBookWork() {

  const { id } = useParams();

  const [md, setmd] = useState({ fullAddress: {}, pincodeId: {} });

  const [ld, setld] = useState(true);

  useEffect(() => {

    (async () => {

      try {

        const { data } = await getSingleBookingWorkforadmin(id);

        //console.log(data);

        if (data.or) {

          setmd(data.result);

        }

      } catch (error) {

        //console.log(error);

      }

      return setld(false);

    })();

  }, [id]);
  return (
    <div>

      {
        ld ? <MyLoader></MyLoader> : <>
        </>
      }

      <Container style={{ margin: "32px 0px" }}>
        <Typography variant='h6'>SingleAdminBookWork {id}</Typography>
      </Container>

      <Container style={{ marginBottom: "16px" }}>
        <Grid container justifyContent='space-between' alignItems='center'>

          <Grid item >
            <Typography>Booking Id {id}</Typography>
          </Grid>
          <Grid item >
            <Link to={`/admin/dashboard/wrk/bookingwork/edit-data/${id}`}>
              edit
            </Link>
          </Grid>
        </Grid>
      </Container>

      <Container>

        <Grid container alignItems='center' justifyContent='space-between' style={{ textTransform: 'capitalize' }}>

          <Grid item >

            <Typography>
              Name of Work {md.workname}
            </Typography>
            <Typography style={{ color: "blue" }}>
              Booking Status {md.bookingStatus}
            </Typography>
            <Typography>
              Date of Booking  {moment(md.createdAt).format('LLL')}
            </Typography>
          </Grid>
          <Grid item >
            <Typography>{md.name} {" "} {md.phoneNumber} {" "} {md.customerId}</Typography>
            <Typography>
              {md.address} {" "}  {md.fullAddress.nameOfCity} {" "}  {md.fullAddress.stateOfCity}{" "} {md.fullAddress.countryOfCity} {" "} {md.pincodeId.pincode}
            </Typography>
          </Grid>

        </Grid>

        <Typography variant='h6' style={{ marginBottom: "32px",marginTop:"24px" }}>
          Image of items list
        </Typography>


        <Grid container alignItems='center' justifyContent='center'>

          <Grid item >
            <img src={md.workimage} alt={md.workname} style={{ maxWidth: "100%" }}></img>
          </Grid>
        </Grid>
      </Container>


    </div>
  )
}
