import { Container, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Typography } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';

import { allAlertAdmin } from '../../../http/index';

import MyLoader from '../../../components/MyLoader/MyLoader';
import moment from 'moment';

export default function AdminNotification() {

    const [d, setD] = useState([{  sellerId: {} }]);

    const [ld, setld] = useState(true);

    useEffect(() => {

        //console.log("use effect calling for alerts for admin");

        (async () => {

            try {
                const { data } = await allAlertAdmin();

                //console.log(data);

                if (data.or) {
                    setD(data.result);
                }

            } catch (error) {

                //console.log(error);
                

            }


            return setld(false);

        })()
    }, []);
    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>

                    <Container>
                        <Typography variant='h6' style={{ textAlign: 'center', textTransform: 'capitalize', marginTop: "32px", marginBottom: "32px" }}>
                            welcome to admin notifications
                        </Typography>
                    </Container>
                    <Container>
                        <TableContainer>

                            <Table>
                                <TableHead>
                                    <TableRow>
                                        <TableCell>
                                            alert id
                                        </TableCell>
                                        <TableCell>
                                            productId
                                        </TableCell>
                                        <TableCell>
                                            supply id
                                        </TableCell>
                                        <TableCell>
                                            seller id
                                        </TableCell>
                                        <TableCell>
                                            seller name 
                                        </TableCell>
                                        <TableCell>
                                            alert status
                                        </TableCell>
                                        <TableCell>
                                            createdAt
                                        </TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>

                                    {
                                        d.map((i) => {

                                            return (<TableRow key={i._id}>
                                                <TableCell>
                                                    <Link to={`/admin/dashboard/notification/single/${i._id}`}>
                                                        {i._id}
                                                    </Link>
                                                </TableCell>
                                                <TableCell>
                                                    <Link to={`/admin/dashboard/productlist/${i.productId}`}>
                                                    {i.productId}
                                                    </Link>
                                                    
                                                </TableCell>
                                                <TableCell>
                                                    
                                                    <Link to={`/dashboard/maplist/${i.supplyId}`}>
                                                    {i.supplyId}
                                                    </Link>
                                                </TableCell>
                                                <TableCell>
                                                    {i.sellerId._id}
                                                </TableCell>
                                                <TableCell>
                                                    {i.sellerId.name}
                                                </TableCell>
                                              
                                                <TableCell style={{color:"blue"}}>
                                                    {i.status}
                                                </TableCell>
                                                <TableCell>
                                                  
                                                    {
                                                        moment(i.createdAt).format("LLL")
                                                    }
                                                </TableCell>
                                            </TableRow>)
                                        })
                                    }
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Container>

                </>
            }

        </div>
    )
}
