
import React, { useEffect, useState } from 'react';

import { othergetAllReturns } from '../../../http/index';
import { Button, Container, Grid, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Typography } from '@material-ui/core';

import { Link } from 'react-router-dom';
import { useHistory } from 'react-router-dom';
import MyLoader from '../../../components/MyLoader/MyLoader';
import moment from 'moment';

export default function AothersReturn() {
  const [ld, setld] = useState(true);
  const [md, setmd] = useState([{ orderId: {}, sellerId: {} }]);

  const history = useHistory();

  useEffect(() => {

    (async () => {

      try {

        const { data } = await othergetAllReturns();

        //console.log(data);

        if (data.or) {

          setmd(data.result);
        }

      } catch (error) {

       // console.log(error);

      }

      return setld(false);

    })()
  }, []);

  return (
    <div>

      {
        ld ? <MyLoader></MyLoader> : <>
          <Container>
            <Grid container alignItems='center' style={{ marginTop: "32px", marginBottom: "32px" }}>

              <Grid item sm>
                <Typography variant='h6'>
                  Aother return
                </Typography>
              </Grid>
              <Grid item >
                <Button onClick={() => {
                  history.push('/admin/dashboard/other/returns/rt-form');
                }} variant='contained' color='primary'>
                  Add Return
                </Button>
              </Grid>
            </Grid>


          </Container>
          <Container>
            <TableContainer>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell>
                      return id
                    </TableCell>
                    <TableCell>
                      name
                    </TableCell>
                    <TableCell>
                     return  amount
                    </TableCell>
                    <TableCell>
                      seller id  
                    </TableCell>
                    <TableCell>
                      sellername 
                    </TableCell>
                    <TableCell>
                      orderId
                    </TableCell>
                    <TableCell>
                      orderStatus
                    </TableCell>
                    <TableCell>
                      Return createAt
                    </TableCell>
                    <TableCell>
                      retStatus
                    </TableCell>


                  </TableRow>
                </TableHead>
                <TableBody>

                  {
                    md.map((i) => {

                      return (<TableRow key={i._id}>
                        <TableCell>

                          <Link to={`/admin/dashboard/other/returns/single/${i._id}`}>
                            {i._id}
                          </Link>
                        </TableCell>
                        <TableCell>
                          {i.name}
                        </TableCell>
                        <TableCell>
                        <i className="fa fa-inr" aria-hidden="true"></i>  {i.amount}
                        </TableCell>
                        <TableCell>
                          {i.sellerId._id}
                        </TableCell>
                        <TableCell>
                          {i.sellerId.name}
                        </TableCell>
                        <TableCell>
                          {i.orderId._id}
                        </TableCell>
                        <TableCell style={{color:"green"}}>
                          {i.orderId.status}
                        </TableCell>
                        <TableCell>
                          {moment(i.createdAt).format('LLL')}
                        </TableCell>
                        <TableCell style={{color:"blue"}}>
                          {i.retStatus}
                        </TableCell>

                      </TableRow>)
                    })
                  }
                </TableBody>
              </Table>
            </TableContainer>
          </Container>
        </>
      }


    </div>
  )
}
