import { Box, Container, Grid, Paper, Typography } from '@material-ui/core';
import React, { useEffect, useState } from 'react';

import { Link } from 'react-router-dom';

import { useLocation } from 'react-router-dom';

import { AdminSupplySearchPrid } from '../../../http/index';

import MyLoader from '../../../components/MyLoader/MyLoader';

export default function AdminSupplySearch() {

  const { search } = useLocation();

  const [sl, setSl] = useState([]);

  const [ld, setld] = useState(true);


  const productId = search.split("=")[1];
  useEffect(() => {

    (async () => {

      try {
        const { data } = await AdminSupplySearchPrid({ productId: productId });

        //console.log(data);

        if (data.or) {
          setSl(data.result);
        }
      } catch (error) {

        //console.log(error);

      }

      return setld(false);
    })()
  }, [productId])

  return (
    <div>

      {
        ld ? <MyLoader></MyLoader> : <>

          <Container>
            <Typography style={{ margin: "16px 0px", textTransform: 'capitalize' }}>
              admin search based on product id       {productId}
            </Typography>
            <Typography variant='h6' style={{marginBottom:"32px",textTransform: 'capitalize' }}>
              Total number of supply found {sl.length}
            </Typography>
          </Container>

          <Container>
            {
              sl.map((i) => {

                return (<Paper key={i._id} style={{ marginBottom: "24px", padding: "8px" }}>

                  <Grid container justifyContent='space-between' alignItems='center' style={{ textTransform: 'capitalize' }}>

                    <Grid item>
                      <Box marginBottom={'12px'}>
                        <Link to={`/dashboard/maplist/${i._id}`}>
                          supply id    {i._id}
                        </Link>
                      </Box>
                      <Typography style={{ color: "blue" }}>
                        supply status {i.status ? "active" : "deactive"}
                      </Typography>
                      <Typography variant='subtitle2'>
                        supply price  <i className="fa fa-inr" aria-hidden="true"></i> {i.price}
                      </Typography>
                      <Typography variant='subtitle2'>
                        stock qutanity {i.inStock}
                      </Typography>
                    </Grid>
                    <Grid item>
                      <Box>
                        <Link to={`/admin/dashboard/productlist/${i.productId._id}`}>
                          <Typography>

                            productId  {i.productId._id}
                          </Typography>
                        </Link>
                      </Box>

                      <Typography>
                        product name {i.productId.name}
                      </Typography>
                      <Typography style={{ color: "green" }}>
                        product status {i.productId.status ? "active" : "deactive"}
                      </Typography>
                      <Typography variant='subtitle2'>
                        mrp  <i className="fa fa-inr" aria-hidden="true"></i> {i.productId.mrp}
                      </Typography>
                      <Typography variant='subtitle2'>
                        selling price  <i className="fa fa-inr" aria-hidden="true"></i> {i.productId.spInGst}
                      </Typography>
                      <Typography variant='subtitle2'>
                        whosele price  <i className="fa fa-inr" aria-hidden="true"></i> {i.productId.spInGstForS}
                      </Typography>

                      <Typography variant='subtitle2'>
                        stock qutanity {i.productId.inStock}
                      </Typography>

                    </Grid>
                    <Grid item>
                      <Typography>
                        seller id {i.sellerId._id}
                      </Typography>
                      <Typography>
                        seller name {i.sellerId.name}
                      </Typography>
                      <Typography variant='subtitle2'>
                        seller phone  {i.sellerId.phone}
                      </Typography>
                      <Typography>
                        name of bussiness  {i.sellerId.nameofbussiness}
                      </Typography>
                      <Typography>
                        adresss of shop {i.sellerId.address1}
                      </Typography>
                    </Grid>
                  </Grid>



                </Paper>)
              })
            }
          </Container>

        </>
      }

    </div>
  )
}
