import { Button, Container, Grid, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, TextField, Typography } from '@material-ui/core';
import moment from 'moment';

import React, { useState, useEffect } from 'react'

import { Link, useHistory } from 'react-router-dom';

import MyLoader from '../../../components/MyLoader/MyLoader';

import { getAllProduct } from '../../../http/index';

export default function AdminProductList() {

    const [ld, setld] = useState(true);

    const history = useHistory();

    const [pr, setPr] = useState([]);

    const [ft, setFt] = useState('');


    function submit() {

        if (!ft) {
            return;
        }

        return history.push(`/admin/dashboard/product/search?q1=${ft}`);


    }
    useEffect(() => {
       // console.log("use effect calling ");

        (async () => {
            try {

                const { data } = await getAllProduct();

               // console.log(data);

                if (data.or) {
                    setPr(data.result);
                }

            } catch (error) {
               // console.log(error);

            }
            return setld(false);
        })();

    }, [])


    return (
        <div>


            {
                ld ? <MyLoader></MyLoader> : <>
                    <Container>

                        <Grid container alignItems='center' style={{ marginTop: "32px", marginBottom: "32px" }}>
                            <Grid item xs>
                                <Typography variant='h6' style={{ textTransform: 'capitalize' }}>
                                    Admin Product list
                                </Typography>
                            </Grid>
                            <Grid item>
                                <Button onClick={() => {
                                    history.push('/admin/dashboard/product-form');
                                }} variant='contained' color='primary'>
                                    Add Product
                                </Button>
                            </Grid>
                        </Grid>

                    </Container>

                    <Container>
                        <Grid container alignItems='center' style={{ marginBottom: "34px" }}>
                            <Grid item xs={12} sm={2}>

                                <Typography style={{ textTransform: 'capitalize', textAlign: 'center' }}>
                                    search product by id
                                </Typography>
                            </Grid>
                            <Grid item xs>
                                <TextField type={'text'} size='small' variant='outlined' value={ft} onChange={(e) => {
                                    setFt(e.target.value);

                                }}>

                                </TextField>
                                <Button onClick={submit} color='primary'>
                                    search
                                </Button>
                            </Grid>

                        </Grid>
                    </Container>

                    <Container>
                        <TableContainer>
                            <Table>
                                <TableHead>
                                    <TableRow>
                                        <TableCell>
                                            product id
                                        </TableCell>
                                        <TableCell>
                                            name
                                        </TableCell>
                                        <TableCell>
                                            mainImage
                                        </TableCell>
                                        <TableCell>
                                            mrp
                                        </TableCell>
                                        <TableCell>
                                            selling price for cutsomer
                                        </TableCell>
                                        <TableCell>
                                            wholse price for admin
                                        </TableCell>
                                        <TableCell>
                                            inStock
                                        </TableCell>
                                        <TableCell>
                                            actualdeliveryCharge
                                        </TableCell>
                                        <TableCell>
                                            deliveryCharge current
                                        </TableCell>
                                        <TableCell>
                                            createdAt
                                        </TableCell>
                                        <TableCell>
                                            status
                                        </TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>

                                    {
                                        pr.map((i) => {

                                            return (<TableRow key={i._id}>
                                                <TableCell>
                                                    <Link to={`/admin/dashboard/productlist/${i._id}`}>
                                                        {i._id}
                                                    </Link>
                                                </TableCell>
                                                <TableCell>
                                                    {i.name}
                                                </TableCell>
                                                <TableCell>
                                                    <img src={i.mainImage} alt={i.name} style={{ maxWidth: "100%", height: "130px" }}>
                                                    </img>
                                                </TableCell>
                                                <TableCell>
                                                <i className="fa fa-inr" aria-hidden="true"></i>   {i.mrp}
                                                </TableCell>
                                                <TableCell>
                                                <i className="fa fa-inr" aria-hidden="true"></i>  {i.spInGst}
                                                </TableCell>
                                                <TableCell>
                                                <i className="fa fa-inr" aria-hidden="true"></i>   {i.spInGstForS}
                                                </TableCell>
                                                <TableCell>
                                                    {i.inStock}
                                                </TableCell>
                                                <TableCell>
                                                <i className="fa fa-inr" aria-hidden="true"></i>  {i.actualDeliveryCharge}
                                                </TableCell>
                                                <TableCell>
                                                <i className="fa fa-inr" aria-hidden="true"></i>   {i.deliveryCharge}
                                                </TableCell>
                                                <TableCell>
                                                    {
                                                        moment(i.createdAt).format('LLL')
                                                    }
                                                </TableCell>
                                                <TableCell style={{ color: "blue" }}>
                                                    {i.status ? "active" : "deactive"}
                                                </TableCell>
                                            </TableRow>)
                                        })
                                    }
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Container>


                </>
            }

        </div>
    )
}
