import { Button, Container, Grid, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, TextField, Typography } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { Link } from 'react-router-dom';
import moment from 'moment';
import MyLoader from '../../../components/MyLoader/MyLoader';
import { allPaymentsOfSeller } from '../../../http/index';

export default function SupplierPaymentPage() {

    const [d, setD] = useState([{ orderId: {} }]);

    const [sr, setSr] = useState('');

    const [er, setEr] = useState(false);

    const [ld, setLd] = useState(true);
    const history = useHistory('');

    function submit(e) {

        e.preventDefault();

        if (!sr) {
            return;
        }

        if (sr.length < 20) {
           // console.log("invalid orderId");
            setEr(true);
            return;
        }

        history.push(`/dashboard/payment/serach/orderId?q1=${sr}`);

    }

    useEffect(() => {

      //  console.log("useEffect calling all payments of seller ");

        (async () => {

            try {

                const { data } = await allPaymentsOfSeller();

                //console.log(data);
                
                if (data.or) {
                    setD(data.result);

                }

            } catch (error) {

               // console.log(error);


            }

            return setLd(false);

        })()
    }, [])
    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>
                    <Container>
                        <Grid container alignItems='center' spacing={2} justifyContent='space-between' style={{ marginTop: "32px", marginBottom: "24px" }}>

                            <Grid item xs sm>
                                <Typography variant='h6' >
                                    SupplierPaymentPage
                                </Typography>
                            </Grid>
                            <Grid item xs sm={3} >
                                <Button color='primary' variant='contained' onClick={() => {
                                    history.push('/dashboard/payment-form');
                                }}>
                                    Add Payment
                                </Button>
                            </Grid>
                        </Grid>

                    </Container>

                    <Container>
                        {
                            er && <Typography style={{ margin: "12px", color: 'red' }}>
                                Invalid orderId
                            </Typography>
                        }

                        <Grid container justifyContent='space-between' alignItems='center'>
                            <Grid container item spacing={1}>
                                <Grid item >

                                    <TextField label='search payment by orderId' variant='outlined' size="small" value={sr} onChange={(e) => {
                                        setSr(e.target.value);
                                    }}>

                                    </TextField>
                                </Grid>
                                <Grid item >
                                    <Button color='primary' onClick={submit} variant='outlined'>
                                        Search
                                    </Button>
                                </Grid>

                            </Grid>

                        </Grid>

                    </Container>

                    <Container>

                        <Typography style={{ textAlign: 'center', marginBottom: "24px", marginTop: "24px" }}>
                            List of All Payments
                        </Typography>
                        <TableContainer>
                            <Table>
                                <TableHead style={{ textTransform: 'capitalize' }}>

                                    <TableRow>
                                        <TableCell>
                                            _id
                                        </TableCell>
                                        <TableCell>
                                            name
                                        </TableCell>
                                        <TableCell>
                                            amount of payment 
                                        </TableCell>
                                        <TableCell>
                                            amount of payment  in bill
                                        </TableCell>
                                        <TableCell>
                                            OrderId
                                        </TableCell>
                                        <TableCell>
                                           total amount of order
                                        </TableCell>
                                        <TableCell>
                                            order status
                                        </TableCell>
                                        <TableCell>
                                            paymentStatus
                                        </TableCell>
                                        <TableCell>
                                            date of payment
                                        </TableCell>
                                    </TableRow>

                                </TableHead>
                                <TableBody>

                                    {
                                        d.map((i) => {
                                            return (
                                                <TableRow key={i._id}>

                                                    <TableCell>
                                                        <Link to={`/dashboard/payment/${i._id}`}>{
                                                            i._id
                                                        }
                                                        </Link>

                                                    </TableCell>
                                                    <TableCell>
                                                        {i.name}
                                                    </TableCell>
                                                    <TableCell>
                                                    <i className="fa fa-inr" aria-hidden="true"></i> {i.amount}
                                                    </TableCell>
                                                    <TableCell>
                                                    <i className="fa fa-inr" aria-hidden="true"></i>   {i.amountofbill}
                                                    </TableCell>
                                                    <TableCell>
                                                        {i.orderId._id}
                                                    </TableCell>
                                                    <TableCell>

                                                    <i className="fa fa-inr" aria-hidden="true"></i>   {i.orderId.totalAmountofOrder}
                                                    </TableCell>
                                                    <TableCell style={{color:"green"}}>
                                                        {i.orderId.status}
                                                    </TableCell>
                                                    <TableCell style={{ color: "blue" }}>
                                                        {
                                                            i.paymentStatus
                                                        }
                                                    </TableCell>
                                                    <TableCell>

                                                        {
                                                            moment(i.createdAt).format('LLL')
                                                        }

                                                    </TableCell>
                                                </TableRow>
                                            )
                                        })
                                    }

                                </TableBody>
                            </Table>

                        </TableContainer>

                    </Container>


                </>
            }


        </div>
    )
}


