import { Container, Typography } from '@material-ui/core';
import React, { useState, useEffect } from 'react'
import { useParams } from 'react-router-dom';

import { getSingleTax } from '../../../http/index';

export default function AdminSingleTax() {

  const { id } = useParams();
  const [md, setmd] = useState({});
  useEffect(() => {

    (async () => {

      try {
        const { data } = await getSingleTax(id);

        //console.log(data);

        if (data.or) {
          return setmd(data.result);

        }
      } catch (error) {
        
        //console.log(error);
      }

    })()

  }, [id]);
  return (
    <div>

      <Container>
        <Typography>admin single tax {id}</Typography>
        <Typography>
          name of tax class {md.name}
        </Typography>
        <Typography>
          rate of tax class {md.rate}
        </Typography>
      </Container>

    </div>
  )
}
