import React, { useEffect, useState } from 'react';
import { useHistory, Link } from 'react-router-dom';
import { Button, Container, Grid, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, TextField, Typography } from '@material-ui/core';

import { allAdminSupply } from '../../../http/index';

import MyLoader from '../../../components/MyLoader/MyLoader';

export default function AdminSupplyList() {

    const [d, setD] = useState([{ productId: {}, sellerId: {}, }]);

    const [t, setmT] = useState('');
    const [ld, setld] = useState(true);

    const history = useHistory();

    function submit() {

        if (!t) {

            return;
        }

        return history.push(`/admin/dashboard/supply/search?q1=${t}`);

    }

    useEffect(() => {


       // console.log("use effect call for supply ");

        (async () => {

            try {
                const { data } = await allAdminSupply();

                //console.log(data);

                if (data.or) {

                    setD(data.result);
                }

            } catch (error) {

              //  console.log(error);

            }

            return setld(false);

        })();



    }, [])
    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>
                    <Container style={{ marginTop: "32px", marginBottom: "32px" }}>
                        <Typography variant='h6' style={{ textTransform: 'capitalize' }}>
                            welcome to admin supply list
                        </Typography>
                    </Container>



                    <Container>
                        <Grid container spacing={3} alignItems='center' style={{ marginTop: "16px", marginBottom: "16px", textTransform: 'capitalize' }}>

                            <Grid item >
                                <Typography>
                                    search all supply based on productId
                                </Typography>
                            </Grid>
                            <Grid item xs>
                                <TextField label='Enter Product Id' value={t} onChange={(e) => {
                                    setmT(e.target.value);
                                }} variant='outlined' size='small' type={'text'}>

                                </TextField>
                                <Button variant='outlined' color='primary' onClick={submit} style={{ marginLeft: "8px" }}>
                                    Search
                                </Button>
                            </Grid>

                        </Grid>
                    </Container>

                    <Container>
                        <TableContainer>
                            <Table>
                                <TableHead>
                                    <TableRow>
                                        <TableCell>
                                            supply id
                                        </TableCell>
                                        <TableCell>
                                            product id
                                        </TableCell>
                                        <TableCell>
                                            product  status
                                        </TableCell>
                                        <TableCell>
                                            supply price
                                        </TableCell>
                                        <TableCell>
                                            instock
                                        </TableCell>
                                        <TableCell>
                                            supply status
                                        </TableCell>
                                        <TableCell>
                                            name of seller
                                        </TableCell>
                                        <TableCell>
                                            name of bussiness
                                        </TableCell>
                                        <TableCell>
                                            address of shop
                                        </TableCell>

                                    </TableRow>
                                </TableHead>
                                <TableBody>

                                    {
                                        d.map((i) => {

                                            return (<TableRow key={i._id}>
                                                <TableCell>
                                                    <Link to={`/dashboard/maplist/${i._id}`}>
                                                        {i._id}
                                                    </Link>
                                                </TableCell>
                                                <TableCell>
                                                    <Link to={`/admin/dashboard/productlist/${i.productId._id}`}>
                                                        {i.productId._id}
                                                    </Link>

                                                </TableCell>
                                                <TableCell style={{ color: "green" }}>
                                                    {i.productId.status ? "active" : "deactive"}

                                                </TableCell>
                                                <TableCell>
                                                <i className="fa fa-inr" aria-hidden="true"></i>  {i.price}
                                                </TableCell>
                                                <TableCell>
                                                    {i.inStock}
                                                </TableCell>
                                                <TableCell style={{ color: "blue" }}>
                                                    {i.status ? "active" : "deactive"}
                                                </TableCell>
                                                <TableCell>
                                                    {i.sellerId.name}
                                                </TableCell>
                                                <TableCell>
                                                    {i.sellerId.nameofbussiness}
                                                </TableCell>
                                                <TableCell>
                                                    {i.sellerId.address1}
                                                </TableCell>
                                            </TableRow>)
                                        })
                                    }
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Container>

                </>
            }


        </div>
    )
}
