import React, { useState } from 'react';

import { sendOtp, verifyOtp } from '../../http/index';

import { useDispatch, useSelector } from 'react-redux';


import { setOtp, setAuth } from '../../store/sellerSlice';
import { Container, Grid, TextField, Typography, Box, Button, } from '@material-ui/core';


const Step = {
    1: StepPhoneNumber,
    2: StepOtp,

};
export default function LoginPage() {

    const [step, setStep] = useState(1);

    const Cstep = Step[step];

    function nextStep() {

        setStep(step + 1);

    }

    return (
        <div>

            <Container >
                <Box padding={'34px 0px'} textAlign='center'>
                    <Typography variant='h6'>
                        Enter Your Otp
                    </Typography>
                </Box>
                <Cstep onClick={nextStep}>
                </Cstep>
            </Container>

        </div>
    )
}


function StepPhoneNumber({ onClick }) {

    const disptach = useDispatch();

    const [ph, setPhone] = useState("");

    const [er, setEr] = useState(false);

    async function submit(e) {

        e.preventDefault();

        // console.log("submit button clicked");
        // console.log(ph);

        const phone = `${ph}`;

        // console.log(phone);
        // validation 


        if (ph.length < 10) {

            setEr(true);
            return;
        }
        

        try {

            const { data } = await sendOtp({ phone });

            //console.log(data);

            disptach(setOtp({ hash: data.hash, phone: data.phone }));

            onClick();

        } catch (error) {

            // console.error(error);
            setEr(true);
        }


    }

    return (<div>


        <Grid container style={{ minHeight: "250px" }} justifyContent='center' alignItems='center'>

            <Grid item >
                <Typography style={{ marginBottom: '48px' }}>
                    Enter your phone to login
                </Typography >
                <Box>
                    {er && <Typography style={{ marginBottom: '48px', color: 'red' }}>
                        Invalid Phone Number
                    </Typography>}
                    <TextField style={{ marginBottom: '48px' }}
                        label="phone number" type="number" size="small" variant="outlined" value={ph} onChange={(e) => {
                            setPhone(e.target.value);
                        }}>

                    </TextField>
                </Box>

                <Box >
                    <Button onClick={submit} color='primary' variant="contained">
                        Login
                    </Button>
                </Box>

            </Grid>
        </Grid>




    </div>
    )

}


function StepOtp() {

    const { phone, hash } = useSelector((state) => state.auth.otp);//


    const disptach = useDispatch();


    const [otp, setOtp] = useState("");

    const [er, setEr] = useState(false);


    async function submit(e) {

        e.preventDefault();

        //console.log("button opt");

        //console.log(otp);

        if (!otp) {
            setEr(true);
            return;
        }
        try {
            const { data } = await verifyOtp({ otp, phone, hash });

            // console.log(data);

            disptach(setAuth({ seller: data.sellerDto }));//{seller}

        } catch (error) {

            // console.log(error);invalid otp

            setEr(true);
        }

    }
    return (
        <div>


            <Grid container style={{ minHeight: "250px" }} justifyContent='center' alignItems='center'>

                <Grid item >
                    <Typography style={{ marginBottom: '16px', textTransform: 'capitalize' }}>
                        Otp Send On this Phone Number {phone}
                    </Typography >
                    <Typography variant='subtitle2' style={{ marginBottom: '48px', textTransform: 'capitalize' }}>
                        Otp is valid for 2 minute only
                    </Typography >
                    <Box>
                        {
                            er && <Typography style={{ marginBottom: '48px', color: 'red' }}>
                                Invalid Otp
                            </Typography>
                        }

                        <TextField style={{ marginBottom: '48px' }}
                            label="otp" type="number" size="small" variant="outlined" value={otp} onChange={(e) => {
                                setOtp(e.target.value);
                            }}>

                        </TextField>
                    </Box>

                    <Box >
                        <Button onClick={submit} color='primary' variant="contained">
                            Submit
                        </Button>
                    </Box>

                </Grid>
            </Grid>

        </div>
    )
}