import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router';

import { Button, Container, Grid, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Typography } from '@material-ui/core';
import MyLoader from '../../../components/MyLoader/MyLoader';

import { getAllCatergory } from '../../../http/index';

export default function AdminProductCatergoryList() {
    const history = useHistory();

    const [cat, setCat] = useState([]);
    const [ld, setld] = useState(true);


    useEffect(() => {
        //console.log("use effect calling for catergory");

        (async () => {
            try {

                const { data } = await getAllCatergory();

                //console.log(data);

                setCat(data);

            } catch (error) {

               // console.log(error);
            }

            return setld(false);

        })();
    }, []);

    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>

                    <Container>

                        <Grid container justifyContent='space-between' style={{ marginTop: "32px", marginBottom: "24px" }}>

                            <Grid item >
                                <Typography variant='h6' style={{ textTransform: 'capitalize' }}>
                                    welcoem to admin product catergory
                                </Typography>
                            </Grid>
                            <Grid item>

                                <Button onClick={() => {
                                    history.push('/admin/dashboard/catergory/form');

                                }} variant='contained' color='primary'>
                                    Add catergory
                                </Button>
                            </Grid>
                        </Grid>


                    </Container>


                    <Container>
                        <TableContainer>
                            <Table>
                                <TableHead>
                                    <TableRow>
                                        <TableCell>
                                            catergory id
                                        </TableCell>
                                        <TableCell>
                                            name
                                        </TableCell>
                                        <TableCell>
                                            des
                                        </TableCell>
                                        <TableCell>
                                            catergoryimage
                                        </TableCell>
                                        <TableCell>
                                            status
                                        </TableCell>

                                    </TableRow>
                                </TableHead>
                                <TableBody>

                                    {
                                        cat.map((i) => {

                                            return (<TableRow key={i._id}>
                                                <TableCell>
                                                    TR {" "}

                                                    {i._id}

                                                </TableCell>
                                                <TableCell>
                                                    {i.name}
                                                </TableCell>
                                                <TableCell>
                                                    {i.des}
                                                </TableCell>
                                                <TableCell>
                                                    <img src={i.catergoryimage} alt={i.name} style={{ maxWidth: "100%", height: "100px" }}>
                                                    </img>

                                                </TableCell>
                                                <TableCell style={{color:"blue"}}>
                                                    {i.status ? "active" : "deactive"}
                                                </TableCell>

                                            </TableRow>)
                                        })
                                    }
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Container>


                </>
            }



        </div>
    )
}
