import React, { useEffect, useState } from 'react';
import { Button, Container, Grid, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Typography } from '@material-ui/core';


import { useHistory } from 'react-router-dom';
import { getAllSubCatergory } from '../../../http/index';
import MyLoader from '../../../components/MyLoader/MyLoader';

export default function AdminSubCatergoryList() {
    const [ld, setld] = useState(true);
    const history = useHistory();

    const [sub, setSub] = useState([]);

    useEffect(() => {

        //console.log("seller subcatergory use effect calling");

        (async () => {
            try {
                const { data } = await getAllSubCatergory();

                //console.log(data);

                setSub(data);

            } catch (error) {

              //  console.log(error);

            }

            return setld(false);

        })();
    }, []);
    return (
        <div>

{
    ld?<MyLoader></MyLoader>:<>

<Container>
                <Grid container justifyContent='space-between' style={{marginTop:"32px",marginBottom:"32px"}}>

                    <Grid item>
                        <Typography variant='h6' style={{textTransform:'capitalize'}}>
                            welcoem to Admin subacetrgory list page
                        </Typography>
                    </Grid>

                    <Grid item>

                        <Button onClick={() => {
                            history.push('/admin/dashboard/sub-catergory/form');

                        }} variant='contained' color='primary'>
                            Add sub catergory
                        </Button>
                    </Grid>
                </Grid>

            </Container>



            <Container>
                <TableContainer>
                    <Table>
                        <TableHead>
                            <TableRow>
                                <TableCell>
                                    sub catergory id
                                </TableCell>
                                <TableCell>
                                    name
                                </TableCell>
                                <TableCell>
                                    des
                                </TableCell>
                                <TableCell>
                                    parent catergory
                                </TableCell>
                                <TableCell>
                                    subcatergoryimage
                                </TableCell>
                                <TableCell>
                                    status
                                </TableCell>

                            </TableRow>
                        </TableHead>
                        <TableBody>

                            {
                                sub.map((i) => {

                                    return (<TableRow key={i._id}>
                                        <TableCell>
                                            TR  {i._id}
                                        </TableCell>
                                        <TableCell>
                                            {i.name}
                                        </TableCell>
                                        <TableCell>
                                            {i.des}
                                        </TableCell>
                                        <TableCell>
                                            {i.parent.name}
                                        </TableCell>
                                        <TableCell>
                                           
                                            <img  src={i.subcatergoryimage} alt={i.name} style={{maxWidth:"100%",height:"100px"}}>
                                            </img>
                                        </TableCell>
                                        <TableCell style={{color:"blue"}}>
                                            {i.status?"active":"deactive"}
                                        </TableCell>

                                    </TableRow>)
                                })
                            }
                        </TableBody>
                    </Table>
                </TableContainer>
            </Container>


            
    </>
}
         
        </div>
    )
}
