import { Button, Container, Grid } from '@material-ui/core'
import React from 'react'
import { useHistory } from 'react-router-dom'


export default function OtherWork() {
  return (
    <div>

      <Container>

        <Grid container justifyContent='center' spacing={2} style={{ margin: "32px 0px" }}>

          <Grid item>

            <MyBtnLink to='/admin/dashboard/other/customers'>
              customers
            </MyBtnLink>
          </Grid>
          <Grid item>

            <MyBtnLink to='/admin/dashboard/other/sellers'>
              sellers
            </MyBtnLink>
          </Grid>
          <Grid item>

            <MyBtnLink to='/admin/dashboard/other/returns'>
              returns
            </MyBtnLink>
          </Grid>

          <Grid item>

            <MyBtnLink to='/admin/dashboard/wrk/bookingwork/all'>
              Booking Works
            </MyBtnLink>
          </Grid>

          <Grid item>

            <MyBtnLink to='/admin/dashboard/wrk/worker/all'>
              Workers
            </MyBtnLink>
          </Grid>

          <Grid item>

            <MyBtnLink to='/admin/dashboard/wrk/work/all'>
              Service works
            </MyBtnLink>
          </Grid>
        </Grid>
      </Container>


    </div>


  )
}


function MyBtnLink({ to, children }) {

  const history = useHistory();

  function s() {

    return history.push(`${to}`);
  }

  return (
    <Button variant='outlined' color='primary' onClick={s}>
      {children}
    </Button>
  )

}