import React, { useEffect, useState } from 'react'
import { Button, Container, Grid, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Typography } from '@material-ui/core';

import { getAllTax } from '../../../http/index';

import MyLoader from '../../../components/MyLoader/MyLoader';

import { useHistory } from 'react-router-dom';

export default function AdminTaxList() {
    const [ld, setld] = useState(true);
    const history = useHistory();

    const [tax, setTax] = useState([]);


    useEffect(() => {
       // console.log("use effect calling");

        (async () => {

            try {
                const { data } = await getAllTax();

                //console.log(data);

                setTax(data);

            } catch (error) {

               // console.log(error);

            }
            return setld(false);
        })();
    }, [])
    return (
        <div>


            {
                ld ? <MyLoader></MyLoader> : <>
                    <Container>
                        <Grid container justifyContent='space-between' style={{ marginTop: "32px", marginBottom: "32px" }}>
                            <Grid item>
                                <Typography variant='h6' style={{ textTransform: 'capitalize' }}>
                                    welcome to admin tax list
                                </Typography>
                            </Grid>
                            <Grid item>
                                <Button onClick={() => {
                                    history.push('/admin/dashboard/tax/form');

                                }} variant='contained' color='primary'>
                                    Add Tax
                                </Button>
                            </Grid>
                        </Grid>

                    </Container>

                    <Container>
                        <TableContainer>
                            <Table>
                                <TableHead>
                                    <TableRow>
                                        <TableCell>
                                            tax id
                                        </TableCell>
                                        <TableCell>
                                            name of tax class
                                        </TableCell>
                                        <TableCell>
                                            rate of tax class
                                        </TableCell>
                                        <TableCell>
                                            des
                                        </TableCell>

                                        <TableCell>
                                            status
                                        </TableCell>

                                    </TableRow>
                                </TableHead>
                                <TableBody>

                                    {
                                        tax.map((i) => {

                                            return (<TableRow key={i._id}>

                                                <TableCell>
                                                    TR     {i._id}

                                                </TableCell>
                                                <TableCell>
                                                    {i.name}
                                                </TableCell>
                                                <TableCell>
                                                    {i.rate}
                                                </TableCell>
                                                <TableCell>
                                                    {i.des}
                                                </TableCell>
                                                <TableCell style={{color:"blue"}}>
                                                    {i.status ? "active" : "deactive"}
                                                </TableCell>

                                            </TableRow>)
                                        })
                                    }
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Container>

                </>
            }


        </div>
    )
}
