import React, { useEffect, useState } from 'react';

import { Button, Container, Grid, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Typography } from '@material-ui/core';

import { allCityStateCountry } from '../../../http/index';
import { useHistory } from 'react-router-dom';
import MyLoader from '../../../components/MyLoader/MyLoader';
import moment from 'moment';

export default function CityStateCountryList() {

    const [d, setD] = useState([{ pincodeOfCity: {} }]);

    const history = useHistory();
    const [ld, setld] = useState(true);

    useEffect(() => {
        //console.log("use effect calling for all list of service");

        (async () => {

            try {
                const { data } = await allCityStateCountry();

                //console.log(data);

                if (data.or) {

                    setD(data.result);

                }
            } catch (error) {
               // console.log(error);
            }

            return setld(false);

        })()

    }, [])
    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>
                    <Container>
                        <Grid container justifyContent='space-between' style={{ marginTop: "32px", marginBottom: "32px" }}>

                            <Grid item >
                                <Typography variant='h6' style={{ textTransform: 'capitalize' }}>
                                    welcome to all list of service area
                                </Typography>
                            </Grid>
                            <Grid item >
                                <Button onClick={() => {
                                    history.push('/admin/dashboard/city/form');
                                }} variant='contained' color='primary'>
                                    Add City
                                </Button>
                            </Grid>
                        </Grid>

                    </Container>
                    <Container>
                        <TableContainer>
                            <Table>
                                <TableHead>
                                    <TableRow>
                                        <TableCell>
                                            city id
                                        </TableCell>
                                        <TableCell>
                                            nameOfCity
                                        </TableCell>

                                        <TableCell>
                                            stateOfCity
                                        </TableCell>

                                        <TableCell>
                                            countryOfCity
                                        </TableCell>
                                        <TableCell>
                                            tierType
                                        </TableCell>

                                        <TableCell>
                                            pincodeOfCity
                                        </TableCell>
                                        <TableCell>
                                            createdAt
                                        </TableCell>

                                    </TableRow>
                                </TableHead>
                                <TableBody>

                                    {
                                        d.map((i) => {

                                            return (<TableRow key={i._id}>
                                                <TableCell>
                                                    TR {i._id}
                                                </TableCell>
                                                <TableCell>
                                                    {i.nameOfCity}
                                                </TableCell>
                                                <TableCell>
                                                    {i.stateOfCity}
                                                </TableCell>
                                                <TableCell>
                                                    {i.countryOfCity}
                                                </TableCell>

                                                <TableCell>
                                                    {i.tierType}
                                                </TableCell>

                                                <TableCell>
                                                    {i.pincodeOfCity.pincode}
                                                </TableCell>
                                                <TableCell>
                                                    {
                                                        moment(i.createdAt).format('LLL')
                                                    }
                                                </TableCell>
                                            </TableRow>)
                                        })
                                    }
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Container>


                </>
            }


        </div>
    )
}
