import { Container } from '@material-ui/core';
import React, { useState } from 'react';
import { Link } from 'react-router-dom';


import { registerWorkServiceforadmin } from '../../../http/index';

export default function AdminWorkForm() {



    const [nameofwork, setnameofwork] = useState('');

    const [workimage, setworkimage] = useState('');

    const [a, seta] = useState(false);



    async function submit(e) {


        e.preventDefault();

        //console.log("submitted button clicked");

        if (!nameofwork || !workimage) {
            return;
        }

        const mydata = new FormData();

        mydata.append("primage", workimage);

        mydata.append("nameofwork", nameofwork.toLowerCase());


        try {
            const { data } = await registerWorkServiceforadmin(mydata);

            // console.log(data);

            if (data.or) {

                return seta(true);
            }


        } catch (error) {

            //console.log(error);

        }

    }




    return (
        <div>

            <Container style={{ margin: "32px 0px" }}>
                <p>welcome to admin work form  </p>

                {
                    a ? <div>

                        <p style={{ color: "green" }}>form is submitted sucessfully</p>
                        <Link to='/admin/dashboard/wrk/work/all'>
                            go to works
                        </Link>
                    </div> : <>
                        <form>
                            <label>
                                nameofwork  <input type={'text'} value={nameofwork} onChange={(e) => {
                                    setnameofwork(e.target.value);

                                }}></input>
                            </label><br></br>
                            <label>
                                image of work  <input type={'file'} onChange={(e) => {
                                    setworkimage(e.target.files[0]);

                                }}></input>
                            </label><br></br>
                            <button onClick={submit}>
                                Submit
                            </button>
                        </form>
                    </>
                }
            </Container>


        </div>



    )
}
