
import { Container, Grid, Typography } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';

import { otherSingleReturn } from '../../../http/index';

import MyLoader from '../../../components/MyLoader/MyLoader';

import moment from 'moment';

export default function AothersSingleReturn() {

  const [ld, setld] = useState(true);
  const [md, setmd] = useState({ orderId: {}, sellerId: {} });

  const { id } = useParams();

  useEffect(() => {

    (async () => {

      try {

        const { data } = await otherSingleReturn(id);

        //console.log(data);

        if (data.or) {

          setmd(data.result);
        }

      } catch (error) {

        //console.log(error);
        

      }

      return setld(false);

    })()
  }, [id]);

  return (
    <div>

      {
        ld ? <MyLoader></MyLoader> : <>
          <Container>
            <Typography variant='h6' style={{ margin: "32px 0px" }}>
              Aothers SingleReturn {id}
            </Typography>

          </Container>

          <Container style={{ textTransform: 'capitalize' }}>
            <Typography style={{ marginBottom: "8px" }}>
              Return id {md._id}
            </Typography>
            <Typography style={{ marginBottom: "8px" }}>
              name of return  {md.name}
            </Typography>
            <Typography variant='subtitle2' style={{ marginBottom: "8px" }}>
              Amount of return  <i className="fa fa-inr" aria-hidden="true"></i> {md.amount}
            </Typography>
            <Typography style={{ marginBottom: "8px", color: "blue" }}>
              return status {md.retStatus}
            </Typography>
            <Typography style={{ marginBottom: "8px" }}>
              return date createAt {moment(md.createdAt).format("LLL")}
            </Typography>
            <Typography style={{ marginBottom: "8px", }}>
              orderId {" "}{md.orderId._id}
            </Typography>
            <Typography  variant='subtitle2' style={{ marginBottom: "8px", }}>
              total amount of order   <i className="fa fa-inr" aria-hidden="true"></i> {md.orderId.totalAmountofOrder}
            </Typography>
            <Typography style={{ marginBottom: "8px", color: 'green' }}>
              order status{" "} {md.orderId.status}
            </Typography>
            <Typography style={{ marginBottom: "8px", }}>
              seller id {" "}{md.sellerId._id}
            </Typography>
            <Typography style={{ marginBottom: "8px", }}>
              seller name {" "}  {md.sellerId.name}
            </Typography>
            <Typography style={{ marginBottom: "8px", }}>
              seller phone {" "}  {md.sellerId.phone}
            </Typography>
            <Typography style={{ marginBottom: "8px",  }}>
              address of shop   {md.sellerId.address1}
            </Typography>
          </Container>

          <Container>
            <Typography variant='h6' style={{ marginBottom: "24px", marginTop:"24px"}}>
              Bills of Retuns
            </Typography>

            <Grid container spacing={3} alignItems='center' >
              <Grid item sm={6} xs={12} container justifyContent='center'>
                <Grid item >
                  <img src={md.retImage} alt="retim" style={{ maxWidth: "100%",height:"600px" }}></img>
                </Grid>


              </Grid>
              <Grid item sm={6} xs={12} container>
                <Grid item justifyContent='center'>
                  <img src={md.billImage} alt="billim" style={{ maxWidth: "100%",height:"600px" }}></img>
                </Grid>

              </Grid>
            </Grid>
          </Container>
        </>
      }


    </div>
  )
}
