import React, { useState } from 'react';

import { Link } from 'react-router-dom';

import { sendTax } from '../../../http/index';

export default function AdminTaxForm() {

    const [name, setN] = useState('');
    const [rate, setR] = useState('');

    const [er, setEr] = useState(false);

    const [des, setD] = useState('');

    const [com, setComp] = useState({
        status: false,
        data: {}
    });


    async function Submit(e) {

        e.preventDefault();
        //console.log("button clicked");

        if (!name || !des) {

            //console.log("valdition error");
            setEr(true);

            return;
        }

        let configdata = {
            name: name.toLowerCase(),
            rate: parseInt(rate),
            des: des.toLowerCase(),
        };

        try {
            const { data } = await sendTax(configdata);

            //console.log(data);

            if (data.pr) {
                setComp({
                    status: true,
                    data: data.d
                });
            }


        } catch (error) {
            //console.log(error);
            
            setEr(true);

        }


    }

    return (
        <div>
            <p>welcome tax form </p>
            {
                com.status ? <div>
                    <p>tax is summbitted sucessfully </p>
                    <p>{com.data._id}</p>
                    <p>{com.data.name}</p>
                    <Link to='/admin/dashboard/tax' >
                        go to tax
                    </Link>
                </div> : <form>
                <div>
                            {
                                er&&<p style={{color:'red'}}>
                                    all feilds are required
                                </p>
                            }
                        </div>
                    <label>
                        Enter Name of Tax Class
                        <input type="text" value={name} onChange={(e) => {
                            setN(e.target.value);


                        }}></input>
                    </label><br></br>

                    <label>
                        Enter Rate of Tax
                        <input type="number" value={rate} onChange={(e) => {
                            setR(e.target.value);


                        }}></input>
                    </label><br></br>

                    <label>
                        Enter descrpitoin of tax
                        <input type="text" value={des} onChange={(e) => {
                            setD(e.target.value);


                        }}></input>
                    </label><br></br>

                    <button onClick={Submit}>
                        Submit
                    </button>
                </form>
            }

        </div>
    )
}
