import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';

import { useLocation } from 'react-router-dom';

import moment from 'moment';

import { searchpaymentbyorderid } from '../../../http/index';

import { Container, Grid, Paper, Typography, Box } from '@material-ui/core';
import MyLoader from '../../../components/MyLoader/MyLoader';

export default function SupplierPaymentSearch() {
    const { search } = useLocation();

    const [sl, setSl] = useState([{ orderId: {} }]);

    const orderId = search.split("=")[1];

    const [ld, setLd] = useState(true);

    useEffect(() => {

        (async () => {

            try {
                const { data } = await searchpaymentbyorderid({ orderId: orderId });

                //console.log(data);

                if (data.or) {
                    setSl(data.result);
                }
            } catch (error) {

                //console.log(error);

            }

            return setLd(false);

        })()
    }, [orderId]);

    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>
                    <Container>
                        <Typography variant='h6' style={{ textTransform: 'capitalize', margin: "32px" }}>
                            welcome to payment based on orderId  {orderId}
                        </Typography>
                    </Container>

                    <Container>

                        {
                            sl.map((i) => {

                                return (
                                    <Paper key={i._id} style={{ padding: "16px", marginBottom: "24px", textTransform: 'capitalize' }}>
                                        <Grid container spacing={2} alignItems='center'>
                                            <Grid item xs={12} sm={5}>
                                                <Box marginBottom={"8px"}>
                                                    <Link to={`/dashboard/payment/${i._id}`}>
                                                        {i._id}
                                                    </Link>
                                                </Box>
                                                <Typography variant='h6' style={{ marginBottom: "8px" }} >
                                                     payment name  {i.name}
                                                </Typography>
                                                <Typography style={{ marginBottom: "8px" }}>
                                                    payment amount  <i className="fa fa-inr" aria-hidden="true"></i> {i.amount}
                                                </Typography>
                                                <Typography style={{ marginBottom: "8px" }}>
                                                    payment amount in bill   <i className="fa fa-inr" aria-hidden="true"></i> {i.amountofbill}
                                                </Typography>
                                                <Typography style={{ marginBottom: "8px", color: 'blue' }}>
                                                    payment status {i.paymentStatus}
                                                </Typography>

                                                <Typography variant='subtitle2' style={{ marginBottom: "8px",  }}>
                                                    createdAt {moment(i.createdAt).format('LLL')}
                                                </Typography>

                                            </Grid>

                                            <Grid item xs={12} sm>
                                                <Typography style={{ marginBottom: "8px" }}>
                                                    order id {i.orderId._id}
                                                </Typography>
                                                <Typography variant='subtitle2' style={{ marginBottom: "8px" }}>
                                                    total amount of order  <i className="fa fa-inr" aria-hidden="true"></i>  {i.orderId.totalAmountofOrder}
                                                </Typography>
                                                <Typography style={{ marginBottom: "8px" ,color:'green'}}>
                                                    order status  {i.orderId.status}
                                                </Typography>
                                            </Grid>
                                        </Grid>
                                    </Paper>
                                )
                            })
                        }

                    </Container>


                </>
            }


        </div>
    )
}
