import { Box, Container, Grid, Paper, Typography } from '@material-ui/core';
import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

import { useLocation } from 'react-router-dom';

import { findProductOnSearch } from '../../../http/index';

import MyLoader from '../../../components/MyLoader/MyLoader';

export default function AdminProductSearch() {

    const [ld, setld] = useState(true);
    const { search } = useLocation();

    const [sl, setSl] = useState([]);

    const productId = search.split("=")[1];

    useEffect(() => {

        (async () => {

            try {
                const { data } = await findProductOnSearch({ id: productId });

                //console.log(data);

                if (data.or) {
                    setSl(data.result);
                }
            } catch (error) {

                //console.log(error);

            }

            return setld(false);

        })()
    }, [productId])
    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>

                    <Container>
                        <Typography variant='h6' style={{ textAlign: 'center', marginTop: "32px", textTransform: 'capitalize', marginBottom: "24px" }}>
                            welcome to search product list {productId}
                        </Typography>
                    </Container>

                    <Container>

                        {
                            sl.map((i) => {

                                return (<Paper key={i._id} style={{ padding: "8px", marginBottom: "24px",textTransform:'capitalize' }}>

                                    <Grid container >
                                        <Grid item xs={12} sm container alignItems='center' justifyContent='center' >

                                            <Grid item>
                                                <img src={i.mainImage} alt={i.name} style={{ maxWidth: "100%", height: "200px" }}></img>
                                            </Grid>

                                        </Grid>
                                        <Grid item xs={12} sm>
                                            <Box style={{marginBottom:"8px"}}>
                                                <Link to={`/admin/dashboard/productlist/${i._id}`}>
                                                    productId    {i._id}
                                                </Link>
                                            </Box>
                                            <Typography variant='subtitle2' style={{marginBottom:"4px"}}>
                                                {i.name}
                                            </Typography>
                                            <Typography style={{ color: "blue",marginBottom:"4px" }}>
                                                product status  {i.status ? "active" : "deactive"}
                                            </Typography>
                                            <Typography  variant='subtitle2' style={{marginBottom:"4px"}}>
                                                wholse price  <i className="fa fa-inr" aria-hidden="true"></i>     {i.spInGstForS}
                                            </Typography>
                                            <Typography  variant='subtitle2' style={{marginBottom:"4px"}}>
                                                selling for cutsomer  <i className="fa fa-inr" aria-hidden="true"></i>  {i.spInGst}
                                            </Typography>
                                            <Typography  variant='subtitle2' style={{marginBottom:"4px"}}>
                                                mrp of product  <i className="fa fa-inr" aria-hidden="true"></i>  {i.mrp}
                                            </Typography>
                                            <Typography  variant='subtitle2' style={{marginBottom:"4px"}}>
                                                stock quatnity   {i.inStock}
                                            </Typography>
                                            <Typography style={{marginBottom:"4px"}}>
                                                actualdeliveryCharge   <i className="fa fa-inr" aria-hidden="true"></i>  {i.actualDeliveryCharge}
                                            </Typography>


                                            <Typography style={{marginBottom:"4px"}}>
                                                deliveryCharge current  <i className="fa fa-inr" aria-hidden="true"></i> {i.deliveryCharge}
                                            </Typography>


                                        </Grid>
                                    </Grid>



                                </Paper>)
                            })
                        }

                    </Container>


                </>
            }


        </div>
    )
}
