import { Container } from '@material-ui/core';
import React, { useState } from 'react';

import { Link } from 'react-router-dom';
import { registerWorkerforadmin } from '../../../http/index';

export default function AdminWorkerForm() {



    const [name, setName] = useState('');
    const [phone, setPhone] = useState('');
    const [address, setAddress] = useState('');
    const [age, setAge] = useState('');
    const [work, setWork] = useState('');

    const [adarNo, setadarNo] = useState('');

    const [bankNo, setBankNo] = useState('');

    const [prImage, setPrImage] = useState('');

    const [a, seta] = useState(false);

    async function submit(e) {


        e.preventDefault();

       // console.log("submitted button clicked");

        if (!name || !phone || !address || !age || !work || !adarNo || !bankNo || !prImage) {

            return;
        }


        const mydata = new FormData();

        mydata.append("primage", prImage);

        mydata.append("name", name.toLowerCase());

        mydata.append("phone", parseInt(phone));

        mydata.append("address", address.toLowerCase());

        mydata.append("age", parseInt(age));

        mydata.append("work", work.toLowerCase());

        mydata.append("adarNo", parseInt(adarNo));

        mydata.append("bankNo", parseInt(bankNo));


        try {

            const { data } = await registerWorkerforadmin(mydata);

            //console.log(data);

            if (data.or) {

               return seta(true);
            }

        } catch (error) {

           // console.log(error);

        }




    }


    return (
        <div>

            <Container style={{ margin: "32px 0px" }}>
                <p>welcome to worker form

                </p>

                {
                    a ? <div>

                        <p style={{ color: "green" }}>form is submitted sucessfully</p>
                        <Link to='/admin/dashboard/wrk/worker/all'>
                            go to workers
                        </Link>
                    </div> : <>
                        <form>
                            <label>
                                name  <input type={'text'} value={name} onChange={(e) => {
                                    setName(e.target.value);

                                }}></input>
                            </label><br></br>
                            <label>
                                phone<input type={'text'} value={phone} onChange={(e) => {
                                    setPhone(e.target.value);
                                }} ></input>
                            </label><br></br>

                            <label>
                                address  <input type={'text'} value={address} onChange={(e) => {
                                    setAddress(e.target.value);

                                }}></input>
                            </label><br></br>
                            <label>
                                age  <input type={'number'} value={age} onChange={(e) => {
                                    setAge(e.target.value);
                                }}></input>
                            </label><br></br>
                            <label>
                                work <input type={'text'} value={work} onChange={(e) => {
                                    setWork(e.target.value);

                                }}></input>
                            </label><br></br>
                            <label>
                                adarNo <input type={'number'} value={adarNo} onChange={(e) => {
                                    setadarNo(e.target.value);

                                }}></input>
                            </label><br></br>
                            <label>
                                bankNo <input type={'number'} value={bankNo} onChange={(e) => {
                                    setBankNo(e.target.value);
                                }}></input>
                            </label><br></br>
                            <label>
                                profile image  <input type={'file'} onChange={(e) => {
                                    setPrImage(e.target.files[0]);

                                }}></input>
                            </label><br></br>

                            <button onClick={submit}>
                                Submit
                            </button>
                        </form>
                    </>
                }

            </Container>

        </div>
    )
}
