import { Container } from '@material-ui/core';
import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { useParams } from 'react-router-dom';
import MyLoader from '../../../components/MyLoader/MyLoader';
import { updateBookingworkforadmin, getdataforeditBookingWorkforadmin } from '../../../http/index';

export default function AdminEditBookingWork() {

  const { id } = useParams();

  const [ld, setld] = useState(true);
  const [md, setmd] = useState({});

  const [s, sets] = useState('pending');

  const [a, seta] = useState(false);

  async function submit(e) {

    e.preventDefault();

    //console.log("button clicked");


    try {
      const { data } = await updateBookingworkforadmin({ id, bookingStatus: s });

     // console.log(data);

      if (data.or) {

        seta(true);

      }
    } catch (error) {

     // console.log(error);

    }



  }

  useEffect(() => {

    (async () => {

      try {

        const { data } = await getdataforeditBookingWorkforadmin(id);

        //console.log(data);

        if (data.or) {

          setmd(data.result);

        }

      } catch (error) {

        //console.log(error);


      }

      return setld(false);
    })();

  }, [id]);


  return (
    <div>


      {
        ld ? <MyLoader></MyLoader> : <>
          <Container>
            <p>AdminEditBookingWork {md._id}
            </p>

            <p> work name {md.workname}</p>
            <p style={{ color: "blue" }}>boking status {md.bookingStatus}</p>
            <p> {md.name} {md.phoneNumber}</p>
            <p>adress {md.address} </p>

            <p>update booking status </p>

            {
              a ? <div>
                <p style={{ color: "green" }}>
                  booking status upade sucessfully
                </p>

                <Link to='/admin/dashboard/wrk/bookingwork/all'>
                  go to booking
                </Link>

              </div> : <>
                <form>
                  <label>
                    upade booking status
                    <select onChange={(e) => {
                      sets(e.target.value);
                    }}>
                      <option>
                        pending
                      </option>
                      <option>
                        confirmed
                      </option>
                      <option>
                        completed
                      </option>
                      <option>
                        cancel
                      </option>
                    </select>
                  </label>
                  <br></br>
                  <button onClick={submit}>
                    submit
                  </button>
                </form>
              </>
            }




          </Container>

        </>
      }


    </div>
  )
}
