import React, { useState, useEffect } from 'react'

import { getallWorkingSerforadmin } from '../../../http/index';

import { Button, Container, Grid, Table, TableBody, TableCell, TableHead, TableRow, Typography } from '@material-ui/core';
import { useHistory } from 'react-router-dom';

import MyLoader from '../../../components/MyLoader/MyLoader';

export default function AdminServiceWork() {

  const [md, setmd] = useState([]);

  const [ld, setld] = useState(true);

  const history = useHistory();
  function tal() {

    history.push('/admin/dashboard/wrk/work-form/add');

  }
  useEffect(() => {

    (async () => {

      try {

        const { data } = await getallWorkingSerforadmin();

        //console.log(data);

        if (data.or) {
          setmd(data.result);

        }
      } catch (error) {

        //console.log(error);

      }

      return setld(false);
    })();

  }, [])


  return (
    <div>


      {
        ld ? <MyLoader></MyLoader> : <>
          <Container style={{ textTransform: 'capitalize', textAlign: 'center', margin: "32px 0px" }}>
            <Typography>Welcome to AdminServiceWork </Typography>
          </Container>

          <Container style={{ textTransform: 'capitalize' }}>


            <Grid container justifyContent='flex-end' style={{ marginBottom: "32px" }}>
              <Grid item >
                <Button onClick={tal} color="primary" variant='contained'>
                  Add Works
                </Button>
              </Grid>
            </Grid>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell>Id</TableCell>
                  <TableCell>nameofwork</TableCell>

                  <TableCell>Status</TableCell>
                  <TableCell>workimage</TableCell>

                </TableRow>
              </TableHead>
              <TableBody>
                {
                  md.map((i) => {
                    return (<TableRow key={i._id}>

                      <TableCell>

                        {i._id}

                      </TableCell>
                      <TableCell>{i.nameofwork}</TableCell>

                      <TableCell style={{ color: "blue" }}>
                        {i.status ? "active" : "deactive"}
                      </TableCell>
                      <TableCell>
                        <img src={i.workimage} alt={i.nameofwork} style={{ width: "200px" }}></img>
                      </TableCell>

                    </TableRow>)
                  })
                }

              </TableBody>
            </Table>
          </Container>
        </>
      }



    </div>
  )
}


