import { Button, Container, Grid, Typography, TextField, MenuItem } from '@material-ui/core';
import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';

import { useParams } from 'react-router-dom';

import MyLoader from '../../../components/MyLoader/MyLoader';
import { getsingleReturndataforedit, updateRetunrstatus } from '../../../http/index';

export default function UpdateSellerReturn() {

    const [ld, setLd] = useState(true);

    const [lt, setLt] = useState(false);

    const { id } = useParams();
    const [mydata, setmydata] = useState({ retStatus: '' });

    let [rtstatus, setRtstatus] = useState('');

    const [er, setEr] = useState(false);
    const history = useHistory();

    async function submit(e) {

        e.preventDefault();

        //console.log("submit button clicked");

        if (!rtstatus) {

            //console.log("validtion error");
            setEr(true);
            return;
        }
        //console.log(rtstatus);

        let condata = {
            id: id,
            retStatus: rtstatus,
        };

        try {
            const { data } = await updateRetunrstatus(condata);

            //console.log(data);

            if (data.or) {

                return setLt(true);
            }
        } catch (error) {

            //console.log(error);

        }

    }




    useEffect(() => {


        (async () => {

            try {
                const { data } = await getsingleReturndataforedit(id);

                console.log(data);

                if (data.or) {

                    setmydata(data.result);

                }
            } catch (error) {

                console.log(error);
            }

            return setLd(false);

        })();

    }, [id]);

    return (
        <div>


            {
                ld ? <MyLoader></MyLoader> : <>
                    <Container>
                        <Typography variant='h6' style={{ textTransform: 'capitalize', textAlign: 'center', margin: "32px 0px" }}>
                            update return status
                            return id {id}

                        </Typography>
                    </Container>


                    {
                        lt ? <Container style={{ textTransform: 'capitalize' }}>

                            <Grid container justifyContent='center'>
                                <Grid item >
                                    <Typography style={{ color: "green", marginBottom: "32px" }}>
                                        retuns is succesfully update
                                    </Typography>

                                    <Button onClick={() => {
                                        history.push('/dashboard/return');
                                    }} variant='contained' color='primary'>
                                        Go To Retunrns
                                    </Button>
                                </Grid>
                            </Grid>

                        </Container> : <>


                            <Container style={{ textTransform: 'capitalize' }}>

                                <Grid container justifyContent='center'>

                                    <Grid item sm={6} xs={12}>
                                        <Typography style={{ marginBottom: "8px" }}>
                                            {mydata.name}
                                        </Typography>

                                        <Typography style={{ marginBottom: "8px" }}>
                                            amount of return   <i className="fa fa-inr" aria-hidden="true"></i> {mydata.amount}
                                        </Typography>
                                        <Typography style={{ marginBottom: "8px", color: 'blue' }}>
                                            return status {mydata.retStatus}
                                        </Typography>


                                        <form>
                                            {
                                                er && <Typography style={{ marginBottom: "24px", color: "red" }}>
                                                    return status feild is required
                                                </Typography>

                                            }
                                            <Grid container alignItems='center' style={{ marginBottom: "24px" }}>

                                                <Grid item >

                                                    <Typography style={{ marginRight: "24px" }}>
                                                        Select Retunrn Status
                                                    </Typography>
                                                </Grid>
                                                <Grid item xs>
                                                    <TextField select style={{ textTransform: 'capitalize', width: "100%" }} onChange={(e) => {
                                                        setRtstatus(e.target.value);
                                                    }} value={rtstatus} variant='outlined' size='small' >

                                                        <MenuItem value={'not seen'}>
                                                            not seen
                                                        </MenuItem>
                                                        <MenuItem value={'seen'}>
                                                            seen
                                                        </MenuItem>

                                                        <MenuItem value={'accepted'}  >
                                                            accepted
                                                        </MenuItem>
                                                        <MenuItem value={'rejected'} >
                                                            rejected
                                                        </MenuItem>
                                                    </TextField>

                                                </Grid>
                                            </Grid>
                                            <Button onClick={submit} variant='contained' color='primary'>
                                                Update Return
                                            </Button>

                                        </form>
                                    </Grid>
                                </Grid>

                            </Container>
                        </>
                    }

                </>
            }



        </div>

    )
}
