import { Button, Container, Grid, } from '@material-ui/core'
import React from 'react'
import { useHistory } from 'react-router-dom';



export default function PrStatic() {
    return (
        <div>

            <Container>


                <Grid container justifyContent='center' spacing={3} style={{ margin: "32px 0px" }}>

                    <Grid item >

                        <MyBtnLink to='/admin/dashboard/productlist'>
                            productlist
                        </MyBtnLink>
                    </Grid>

                    <Grid item >


                        <MyBtnLink to='/admin/dashboard/catergory'>
                            catergory
                        </MyBtnLink>
                    </Grid>

                    <Grid item >


                        <MyBtnLink to='/admin/dashboard/sub-catergory'>
                            sub catergory
                        </MyBtnLink>
                    </Grid>
                    <Grid item >


                        <MyBtnLink to='/admin/dashboard/tax'>
                            tax
                        </MyBtnLink>
                    </Grid>
                    <Grid item >


                        <MyBtnLink to='/admin/dashboard/pin/all'>
                            pincode
                        </MyBtnLink>
                    </Grid>

                    <Grid item >

                        <MyBtnLink to='/admin/dashboard/city/all'>
                            city
                        </MyBtnLink>

                    </Grid>

                    <Grid item >

                        <MyBtnLink to='/admin/dashboard/supply/all'>
                            supply
                        </MyBtnLink>

                    </Grid>

                </Grid>
            </Container>
        </div>
    )
}


function MyBtnLink({ to, children }) {

    const history = useHistory();
    function s() {

        history.push(`${to}`);
    }
    return (
        <Button onClick={s} color='primary'>
            {children}
        </Button>
    )
}