import { Button, Container, Grid, TextField, Typography, Box } from '@material-ui/core';
import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';

import { registerPayment } from '../../../http/index';

export default function SupplierPaymentForm() {

    const [name, setN] = useState('');
    const [amount, setA] = useState('');
    const [sellerName, setSellerN] = useState('');
    const [orderId, setOrderId] = useState('');
    const [amountofbill, setAmountofbill] = useState('');

    const [d, setD] = useState(false);

    const [er, setEr] = useState(false);

    const history = useHistory();

    async function submit(e) {

        e.preventDefault();
       // console.log("button clicked");

        //valdition
        if (!name || !amount || !sellerName || !orderId || !amountofbill) {

            //console.log("validation error");
            setEr(true);
            return;
        }

        const myobj = {
            name: name.toLowerCase(),
            amount: parseInt(amount),
            sellerName: sellerName.toLowerCase(),
            orderId,
            amountofbill: parseInt(amountofbill),
        };
        try {
            const { data } = await registerPayment(myobj);

           // console.log(data);

            if (data.or) {

                return setD(true);
            }

        } catch (error) {

           // console.log(error);

           setEr(true);
        }
    }

    return (
        <div>
            <Container style={{ padding: "34px 0px" }}>
                <Typography variant='h6' style={{ textAlign: 'center' }}>
                    SupplierPaymentForm
                </Typography>
            </Container>

            {
                d ? <Container style={{ padding: "34px 0px", textAlign: 'center' }} >
                    <Typography style={{ marginBottom: '24px', color: 'green' }}>
                        payment form submmitted sucessfully
                    </Typography>
                    <Button onClick={() => {
                        history.push('/dashboard/payment');
                    }} variant='outlined' color='primary'>
                        Go  Payments
                    </Button>
                </Container> :
                    <Container style={{ textTransform: "capitalize" }}>
                        {er && <Typography style={{ textAlign: 'center', color: 'red',marginBottom:"24px" }}>
                            Validation Error 
                        </Typography>}

                        <form>
                            <Grid container justifyContent='center'>

                                <Grid item sm={6} xs={12}>

                                    <Grid container spacing={3} alignItems='center' style={{ marginBottom: "24px" }}>

                                        <Grid item >
                                            <Typography   >
                                                name of payment
                                            </Typography>
                                        </Grid>
                                        <Grid item xs>
                                            <TextField type="text" size='small' variant='outlined' style={{ width: "100%" }} value={name} onChange={(e) => {
                                                setN(e.target.value);

                                            }}>
                                            </TextField>
                                        </Grid>

                                    </Grid>
                                    <Grid container spacing={3} alignItems='center' style={{ marginBottom: "24px" }}>

                                        <Grid item >
                                            <Typography >
                                                amount of payment
                                            </Typography>
                                        </Grid>
                                        <Grid item xs>
                                            <TextField type="number" size='small' variant='outlined' style={{ width: "100%" }} value={amount} onChange={(e) => {
                                                setA(e.target.value);
                                            }}>

                                            </TextField>
                                        </Grid>

                                    </Grid>
                                    <Grid container spacing={3} alignItems='center' style={{ marginBottom: "24px" }}>

                                        <Grid item >
                                            <Typography >
                                                amount of bill in payment
                                            </Typography>
                                        </Grid>
                                        <Grid item xs>
                                            <TextField type="number" size='small' variant='outlined' style={{ width: "100%" }} value={amountofbill} onChange={(e) => {
                                                setAmountofbill(e.target.value);
                                            }}>

                                            </TextField>
                                        </Grid>

                                    </Grid>
                                    <Grid container spacing={3} alignItems='center' style={{ marginBottom: "24px" }}>

                                        <Grid item >
                                            <Typography >
                                                enter order id
                                            </Typography>
                                        </Grid>
                                        <Grid item xs>
                                            <TextField size='small' variant='outlined' style={{ width: "100%" }} value={orderId} onChange={(e) => {
                                                setOrderId(e.target.value);
                                            }} >

                                            </TextField>
                                        </Grid>

                                    </Grid>
                                    <Grid container spacing={3} alignItems='center' style={{ marginBottom: "24px" }}>

                                        <Grid item >
                                            <Typography >
                                                name of Person Name
                                            </Typography>
                                        </Grid>
                                        <Grid item xs>
                                            <TextField type="text" size='small' variant='outlined' style={{ width: "100%" }} value={sellerName} onChange={(e) => {
                                                setSellerN(e.target.value);
                                            }}>

                                            </TextField>
                                        </Grid>

                                    </Grid>

                                    <Box>
                                        <Button onClick={submit} variant='contained' color='primary'>
                                            Submit
                                        </Button>
                                    </Box>
                                </Grid>

                            </Grid>
                        </form>
                    </Container>
            }


        </div>
    )
}
