import { Container, Grid, Typography, Button } from '@material-ui/core';
import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router';
import { useHistory } from 'react-router-dom';

import MyLoader from '../../../components/MyLoader/MyLoader';

import { getSinglePaymentofSeller } from '../../../http/index';


export default function SupplierSinglePaymentPage() {

    const { id } = useParams();

    const [md, setmd] = useState({ orderId: {} });

    const [ld, setLd] = useState(true);

    const history = useHistory();

    function EDpay() {

        history.push(`/dashboard/payment-update/${id}`);
    }


    useEffect(() => {
        (async () => {

            try {

                const { data } = await getSinglePaymentofSeller(id);

                //console.log(data);


                if (data.or) {

                    setmd(data.result);

                }
            } catch (error) {

                //console.log(error);
                

            }

            return setLd(false);

        })();

    }, [id]);

    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>
                    <Container>


                        <Grid container alignItems='center' style={{ margin: "32px 0px", textTransform: 'capitalize' }}>
                            <Grid item xs>
                                <Typography>Single payment  Details Page {id} </Typography>
                            </Grid>

                            <Grid item >

                                <Button onClick={EDpay} color='secondary' variant='contained'>
                                    Edit Payment
                                </Button>
                            </Grid>
                        </Grid>

                    </Container>

                    <Container style={{ textTransform: 'capitalize' }}>
                        <Typography style={{ marginBottom: "8px" }}>
                            Payment Id {md._id}
                        </Typography>
                        <Typography style={{ marginBottom: "8px" }}>
                            name  of payment {md.name}
                        </Typography>
                        <Typography variant='subtitle2' style={{ marginBottom: "8px" }}>
                            amount of payment  <i className="fa fa-inr" aria-hidden="true"></i>   {md.amount}
                        </Typography>
                        <Typography variant='subtitle2' style={{ marginBottom: "8px" }}>
                            amount of payment in bill   <i className="fa fa-inr" aria-hidden="true"></i>  {md.amountofbill}
                        </Typography>
                        <Typography style={{ marginBottom: "8px" }}>
                            Payment recciver     {md.sellerName}
                        </Typography>
                        <Typography style={{ marginBottom: "8px", color: 'blue' }}>
                            status of payment  {md.paymentStatus}
                        </Typography>
                        <Typography style={{ marginBottom: "8px" }}>
                            orderId {md.orderId._id}
                        </Typography>
                        <Typography style={{ marginBottom: "8px" }}>
                            order total  amount  <i className="fa fa-inr" aria-hidden="true"></i> {md.orderId.totalAmountofOrder}
                        </Typography>
                        <Typography style={{ marginBottom: "8px", color: 'green' }}>
                            order status  {md.orderId.status}
                        </Typography>
                    </Container>
                </>
            }


        </div>
    )
}
