import { createSlice } from '@reduxjs/toolkit';

const initialState = {
    
    isAuth: false,
    seller:null,
    otp: {
        phone: '',
        hash: '',
    }

};

export const authSlice = createSlice({

    name: 'auth',
    initialState,
    reducers: {
        setAuth: (state, action) => {
            // state={isAuth:"",seller:{}}

            const { seller } = action.payload;//{seller:{}}

            state.isAuth = true;

            state.seller = seller;

        },
        setOtp: (state, action) => {
            const { hash, phone } = action.payload;// pura data

            state.otp.hash = hash;
            state.otp.phone = phone;

        },
        setLogout: (state, action) => {

            state.isAuth = false;
            state.seller = action.payload;

        },

    },
});

export const { setAuth, setOtp, setLogout } = authSlice.actions;

export default authSlice.reducer;



