
import { Button, Container, Grid, TextField, Typography } from '@material-ui/core';
import React, { useState } from 'react'
import { useHistory } from 'react-router-dom';



export default function AdminDashboard() {

    const [prId, setPrId] = useState('');

    const history = useHistory();

    function submit() {

        //console.log(prId);

        if (!prId) {

            return;
        }

        return history.push(`/admin/dashboard/supply/search?q1=${prId}`);

    }

    return (
        <div>



            <Container >
                <Typography variant='h6' style={{ textAlign: 'center', textTransform: 'capitalize', marginTop: "34px", marginBottom: "24px" }}>
                    welcome to admin dashboard
                </Typography>
            </Container>

            <Container>
                <Grid container justifyContent='flex-end' alignItems='center' spacing={2}>
                    <Grid item>

                        <Typography style={{ textTransform: 'capitalize' }}>
                            search supply by product id
                        </Typography>
                    </Grid>
                    <Grid item>

                        <TextField type={'text'} variant='outlined' size='small' value={prId} onChange={(e) => {
                            setPrId(e.target.value);

                        }}>

                        </TextField>

                        <Button onClick={submit} variant='contained' color='primary' style={{ marginLeft: '8px' }}>
                            search
                        </Button>
                    </Grid>
                </Grid>


            </Container>


            <Container>

                <Typography style={{ margin: "24px 0px", textAlign: 'center' }}>
                    Admin Dashboard
                </Typography>
            </Container>


        </div>
    )
}
