import React, { useState, useEffect } from 'react'
import { useParams } from 'react-router';
import { Link } from 'react-router-dom';

import { getdataforeditofproduct, updateProduct } from '../../../http/index';


import MyLoader from '../../../components/MyLoader/MyLoader';

export default function AdminProductUpdate() {
    const [ld, setld] = useState(true);
    const [md, setmd] = useState({});

    const { id } = useParams();


    let [name, setName] = useState('');
    let [des, setDes] = useState('');
    let [prStatus, setPrStatus] = useState('active');
    let [mrp, setMrp] = useState('');
    let [inStock, setInStock] = useState('');
    let [actualDeliveryCharge, setAcDelivery] = useState('');
    let [customerP, setCusP] = useState('');
    let [sellerP, setSellerP] = useState('');

    let [mystauts] = useState('');

    let [or, setOr] = useState(false);


    async function submit(e) {

        e.preventDefault();

        if (!name) {
            name = md.name;

        }
        if (!des) {
            des = md.des;

        }
        if (prStatus) {

            if (prStatus === 'active') {
                mystauts = true;
            } else {
                mystauts = false;
            }
        } else {
            mystauts = md.status;
        }

        if (!mrp) {
            mrp = md.mrp;

        }

        if (!inStock) {
            inStock = md.inStock;
        }

        if (!actualDeliveryCharge) {
            actualDeliveryCharge = md.actualDeliveryCharge;
        }

        if (!customerP) {
            customerP = md.spInGst;
        }

        if (!sellerP) {
            sellerP = md.spInGstForS;
        }



        let myobj = {
            id: id,
            name: name.toLowerCase(),
            des: des.toLowerCase(),
            status: mystauts,
            mrp: parseInt(mrp),
            inStock: parseInt(inStock),
            actualDeliveryCharge: parseInt(actualDeliveryCharge),
            customerP: parseInt(customerP),
            sellerP: parseInt(sellerP),

        };


        try {
            const { data } = await updateProduct(myobj);

           // console.log(data);

            if (data.or) {
                return setOr(true);
            }
        } catch (error) {

           // console.log(error);
        }

    }

    useEffect(() => {

        (async () => {
            try {

                const { data } = await getdataforeditofproduct(id);

               // console.log(data);

                if (data.or) {

                    setmd(data.result);

                }
            } catch (error) {

                //console.log(error);

            }
            return setld(false);

        })()

    }, [id]);

    return (
        <div>
            {
                ld ? <MyLoader></MyLoader> : <>
                    <p>AdminProductUpdate by id </p>
                    <p>{md._id}</p>
                    <p>{md.name}</p>
                    <p>{md.brand}</p>
                    <p>mrp {md.mrp}</p>
                    <p>{md.status ? "active" : "decative"}</p>

                    {
                        or ? <div>
                            <p>product update suceesflly</p>
                            <Link to='/admin/dashboard/productlist'>
                                go products
                            </Link>

                        </div> : <div>
                            <form>
                                <label>
                                    name of product <input type={'text'} defaultValue={md.name} onChange={(e) => {
                                        setName(e.target.value);
                                    }}>
                                    </input>
                                </label><br></br>
                                <label>
                                    des of product <input type={'text'} defaultValue={md.des} onChange={(e) => {
                                        setDes(e.target.value);
                                    }}>
                                    </input>
                                </label><br></br>
                                <label>
                                    mrp of product <input type={'number'} defaultValue={md.mrp} onChange={(e) => {
                                        setMrp(e.target.value);
                                    }}>
                                    </input>
                                </label><br></br>
                                <label>
                                    product inStock <input type={'number'} defaultValue={md.inStock} onChange={(e) => {
                                        setInStock(e.target.value);

                                    }}>
                                    </input>
                                </label><br></br>
                                <label>
                                    product actualDeliveryCharge
                                    <input type={'number'} defaultValue={md.actualDeliveryCharge} onChange={(e) => {
                                        setAcDelivery(e.target.value);
                                    }}>
                                    </input>
                                </label><br></br>
                                <label>
                                    select status of product
                                    <select onChange={(e) => {
                                        setPrStatus(e.target.value);
                                    }}>
                                        <option>
                                            active
                                        </option>
                                        <option>
                                            deactive
                                        </option>
                                    </select>
                                </label><br></br>

                                <label>
                                    customer price of product customerP
                                    <input type={'number'} defaultValue={md.spInGst} onChange={(e) => {
                                        setCusP(e.target.value);
                                    }}>
                                    </input>
                                </label><br></br>
                                <label>
                                    seller  price of product sellerP
                                    <input type={'number'} defaultValue={md.spInGstForS} onChange={(e) => {
                                        setSellerP(e.target.value);
                                    }}>
                                    </input>
                                </label><br></br>

                                <button onClick={submit}>
                                    submit
                                </button>
                            </form>
                        </div>
                    }

                </>
            }


        </div>
    )
}
