import { Button, Container, Grid, TextField, Typography } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';

import MyLoader from '../../components/MyLoader/MyLoader';

import { getSellerforedit, updateSellerProfile } from '../../http/index';

export default function EditFormPage() {
    const [mydata, setmydata] = useState({
        name: "",
        nameofbussiness: "",
        address1: "",
        des: "",
        address2: "",
        catergory: "",
        fullAddress: {},
        pincodeId: {},
    });

    const [st, setSt] = useState(false);

    let [name, setName] = useState('');

    let [nameofbussiness, setnameofbussiness] = useState('');

    let [address1, setaddress1] = useState('');

    let [des, setdes] = useState('');

    const history = useHistory();


    let [catergory, setcatergory] = useState('');


    const [Ld, setLd] = useState(true);

    async function submit(e) {

        e.preventDefault();

        //console.log("submit button clicked");

        let condata = {
            name,
            nameofbussiness,
            catergory,
            des,
            address1,

        };

        //console.log(condata);

        try {

            const { data } = await updateSellerProfile(condata);

            //console.log(data);

            if (data.or) {

                return setSt(true);

            }
        } catch (error) {

           // console.log(error);

        }



    }

    useEffect(() => {


        (async () => {


            try {

                const { data } = await getSellerforedit();

                //console.log(data);

                const { result } = data;

                if (data.or) {

                    setmydata(result);
                    setName(result.name);
                    setnameofbussiness(result.nameofbussiness);
                    setcatergory(result.catergory);
                    setdes(result.des);
                    setaddress1(result.address1);

                }
            } catch (error) {

                //console.log(error);
                

            }

            return setLd(false);

        })();

    }, [])

    return (
        <div>

            {
                Ld ? <MyLoader></MyLoader> : <>

                    <Container>

                        <Typography variant='h6' style={{ textAlign: 'center', margin: "24px 0px", textTransform: 'capitalize' }}>
                            edit form seller ProfilePage
                        </Typography>
                    </Container>


                    {
                        st ? <Container>
                            <Grid container justifyContent='center' alignItems='center' style={{ textTransform: 'capitalize', minHeight: "200px" }}>
                                <Grid item >
                                    <Typography style={{ marginBottom: "32px" }}>
                                        Profile Updated successfully

                                    </Typography>
                                    <Button color='primary' onClick={() => {

                                        history.push('/profile');
                                    }} variant='contained'>
                                        profile
                                    </Button>
                                </Grid>
                            </Grid>



                        </Container> : <Container>

                            <form>

                                <Grid container justifyContent='center' style={{ textTransform: 'capitalize' }}>
                                    <Grid item sm={6}>
                                        <Grid container spacing={1} alignItems='center' style={{ marginBottom: "24px" }}>
                                            <Grid item >
                                                <Typography>
                                                    name
                                                </Typography>
                                            </Grid>
                                            <Grid item xs>

                                                <TextField style={{ width: "100%" }} type={'text'} size="small" variant='outlined' value={name} onChange={(e) => {
                                                    setName(e.target.value);
                                                }}>

                                                </TextField>

                                            </Grid>
                                        </Grid>
                                        <Grid container spacing={1} alignItems='center' style={{ marginBottom: "24px" }}>
                                            <Grid item >
                                                <Typography>
                                                    nameofbussiness
                                                </Typography>

                                            </Grid>
                                            <Grid item xs>

                                                <TextField style={{ width: "100%" }} type={'text'} size="small" variant='outlined' value={nameofbussiness} onChange={(e) => {
                                                    setnameofbussiness(e.target.value);
                                                }}>

                                                </TextField>

                                            </Grid>
                                        </Grid>



                                        <Grid container spacing={1} alignItems='center' style={{ marginBottom: "24px" }}>
                                            <Grid item >
                                                <Typography>
                                                    catergory
                                                </Typography>


                                            </Grid>
                                            <Grid item xs>

                                                <TextField style={{ width: "100%" }} type={'text'} size="small" variant='outlined' value={catergory} onChange={(e) => {
                                                    setcatergory(e.target.value);
                                                }}>

                                                </TextField>

                                            </Grid>
                                        </Grid>






                                        <Grid container spacing={1} alignItems='center' style={{ marginBottom: "24px" }}>
                                            <Grid item >
                                                <Typography>
                                                    description
                                                </Typography>




                                            </Grid>
                                            <Grid item xs>

                                                <TextField style={{ width: "100%" }} type={'text'} size="small" variant='outlined' value={des} onChange={(e) => {
                                                    setdes(e.target.value);
                                                }}>

                                                </TextField>


                                            </Grid>
                                        </Grid>


                                        <Grid container spacing={1} alignItems='center' style={{ marginBottom: "24px" }}>
                                            <Grid item >
                                                <Typography>
                                                    address
                                                </Typography>
                                            </Grid>
                                            <Grid item xs>
                                                <TextField style={{ width: "100%" }} type={'text'} size="small" variant='outlined' value={address1} onChange={(e) => {
                                                    setaddress1(e.target.value);
                                                }}>

                                                </TextField>

                                            </Grid>
                                        </Grid>

                                        <Typography style={{ marginBottom: "24px" }}>
                                            {mydata.fullAddress.nameOfCity} {" "} {mydata.fullAddress.stateOfCity} {" "} {mydata.fullAddress.countryOfCity} {" "} {mydata.pincodeId.pincode}
                                        </Typography>


                                        <Button onClick={submit} color='primary' variant='contained'>
                                            Update
                                        </Button>

                                    </Grid>
                                </Grid>


                            </form>
                        </Container>

                    }



                </>
            }


        </div>
    )
}
