import axios from 'axios';
import { useEffect, useState } from 'react';

import { useDispatch } from 'react-redux';

import { setAuth } from '../store/sellerSlice';

export function useLoadingWithRefresh() {
    const [loading, setLoading] = useState(true);

    const dispatch = useDispatch();
    useEffect(() => {

        //console.log("refresh useeffecting calling");

        (async () => {

            try {
                const { data } = await axios.get('https://sellerstoreclubs.com/api/seller/refresh', { withCredentials: true });

                //console.log(data);

                const{sellerDto}=data;
                // set redux 
               
                dispatch(setAuth({ seller: sellerDto }));
                
                setLoading(false);

            } catch (error) {
                //console.log(error);

                setLoading(false);

            }

        })();
        
    }, [dispatch])

    return {
        loading
    }
}

