import { Button, Container, Grid, Typography } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';

import { getSellerProfile } from '../../http/index';

import MyLoader from '../../components/MyLoader/MyLoader';

export default function ProfilePage() {

    const [dt, setDt] = useState({ fullAddress: {}, pincodeId: {} });

    const [myld, setmyld] = useState(true);

    const history = useHistory();

    useEffect(() => {
        
        (async () => {

            try {
                const { data } = await getSellerProfile();

               //console.log(data);

                if (data.or) {

                    setDt(data.result);
                }

            } catch (error) {

                //console.log(error);
                
            }

            return setmyld(false);


        })();
    }, [])

    function editSellerP() {

        history.push('/profile/edit');

    }
    return (
        <div>

            {
                myld ? <MyLoader></MyLoader> : <>
                    <Container>

                        <Grid container justifyContent='space-between' alignItems='center' style={{ marginTop: '34px', textTransform: 'capitalize' }}>

                            <Grid item xs>

                                <Typography variant='h6'>
                                    welcome to seller profile page
                                </Typography>
                            </Grid>
                            <Grid item xs={5} sm={2}>
                                <Button onClick={editSellerP} variant='contained' color='secondary'>
                                    edit profile
                                </Button>
                            </Grid>
                        </Grid>
                    </Container>

                    <Container style={{ textTransform: 'capitalize' }}>
                        <Grid container style={{ marginTop: "24px" }}>

                            <Grid item >
                                <Typography variant='h6' style={{ marginBottom: "8px" }}>
                                    {dt.name}
                                </Typography>
                                <Typography style={{ marginBottom: "8px" }}>
                                    phone number   {dt.phone}
                                </Typography>
                                <Typography variant='h6' style={{ marginBottom: "8px" }}>
                                    gst number of seller {dt.gst}
                                </Typography>
                                <Typography style={{ marginBottom: "8px" }}>
                                    Name of Shop  {dt.nameofbussiness}
                                </Typography>
                                <Typography variant='subtitle2' style={{ marginBottom: "8px" }}>
                                    Address of Shop  {dt.address1}
                                </Typography>
                                <Typography  style={{ marginBottom: "8px" }}>
                                    {dt.fullAddress.nameOfCity}  {dt.fullAddress.stateOfCity} {dt.fullAddress.countryOfCity}
                                </Typography>
                                <Typography style={{ marginBottom: "8px" }}>
                                    pincode {dt.pincodeId.pincode}
                                </Typography>

                            </Grid>

                        </Grid>

                    </Container>

                </>
            }


        </div>
    )
}


