
import React, { useEffect, useState } from 'react';

import { othergetAllSeller } from '../../../http/index';
import { Container, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Typography } from '@material-ui/core';
import MyLoader from '../../../components/MyLoader/MyLoader';

import moment from 'moment';
export default function AotherSeller() {
  const [ld, setld] = useState(true);
  const [md, setmd] = useState([{ fullAddress: {}, pincodeId: {} }]);

  useEffect(() => {

    (async () => {

      try {

        const { data } = await othergetAllSeller();

        //console.log(data);

        if (data.or) {

          setmd(data.result);
        }

      } catch (error) {

        //console.log(error);

      }

      return setld(false);

    })()
  }, []);

  return (
    <div>

      {
        ld ? <MyLoader></MyLoader> : <>
          <Container>
            <Typography variant='h6' style={{ marginTop: "32px", marginBottom: "32px", textAlign: 'center' }}>
              AotherSeller
            </Typography>
          </Container>
          <Container>
            <TableContainer>
              <Table>
                <TableHead>
                  <TableRow style={{ textTransform: 'capitalize' }}>
                    <TableCell>
                      seller id
                    </TableCell>
                    <TableCell>
                      name
                    </TableCell>
                    <TableCell>
                      phone
                    </TableCell>
                    <TableCell>
                      nameofbussiness
                    </TableCell>
                    <TableCell>
                      gst
                    </TableCell>
                    <TableCell>
                      address1
                    </TableCell>
                    <TableCell>
                      fullAddress
                    </TableCell>
                    <TableCell>
                      CreateAt
                    </TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>

                  {
                    md.map((i) => {

                      return (<TableRow key={i._id}>
                        <TableCell>

                          {i._id}

                        </TableCell>
                        <TableCell>
                          {i.name}
                        </TableCell>
                        <TableCell>
                          {i.phone}
                        </TableCell>
                        <TableCell>
                          {i.nameofbussiness}
                        </TableCell>
                        <TableCell>
                          {i.gst}
                        </TableCell>
                        <TableCell>
                          {i.address1}
                        </TableCell>
                        <TableCell>
                          {i.fullAddress.nameOfCity} {""}   {i.fullAddress.stateOfCity} {" "} {i.fullAddress.countryOfCity}
                          {" ,"} {i.pincodeId.pincode}
                        </TableCell>
                        <TableCell>

                          {
                            moment(i.createdAt).format('LLL')
                          }

                        </TableCell>
                      </TableRow>)
                    })
                  }
                </TableBody>
              </Table>
            </TableContainer>
          </Container>



        </>
      }



    </div>
  )
}
