import React, { useState } from 'react';
import { Link } from 'react-router-dom';


import { sendPincode } from '../../../http/index';

export default function PinCodeForm() {

    const [a, setA] = useState(false);

    const [name, setN] = useState('');
    const [des, setD] = useState('');
    const [totalP, setTotal] = useState('');
    const [pincode, setP] = useState('');
    const [er, setEr] = useState(false);
    async function submit(e) {

        e.preventDefault();
       // console.log("button clicked");

        if (!name || !des || !totalP || !pincode) {

           // console.log("valdition error");
            setEr(true);
            return;
        }

        try {
            const { data } = await sendPincode({ name, des, totalP, pincode });

           // console.log(data);

            if (data.or) {
                return setA(true);
            }
        } catch (error) {

           // console.log(error);
            setEr(true);

        }
    }
    return (
        <div>
            <p>
                welcome to pincode form page
            </p>

            {
                a ? <div>
                    <p>form is submit sucessfully</p>
                    <Link to='/admin/dashboard/pin/all'>
                        go to pincode
                    </Link>
                </div> : <form>
                    <div>
                        {
                            er && <p style={{ color: 'red' }}>
                                all feilds are required
                            </p>
                        }
                    </div>
                    <label>
                        name of pincode <input type="text" value={name} onChange={(e) => {
                            setN(e.target.value);
                        }}></input>
                    </label><br></br>
                    <label>
                        description  of pincode <input type="text" value={des} onChange={(e) => {
                            setD(e.target.value);

                        }}></input>
                    </label><br></br>
                    <label>
                        total polulation of pincode <input type="number" value={totalP} onChange={(e) => {
                            setTotal(e.target.value);
                        }}></input>
                    </label><br></br>
                    <label>
                        number pincode <input type="number" value={pincode} onChange={(e) => {
                            setP(e.target.value);
                        }}></input>
                    </label><br></br>
                    <button onClick={submit}>
                        submit
                    </button>
                </form>
            }

        </div>
    )
}
