import { Box, Button, Container, Grid, Typography } from '@material-ui/core';
import React, { useState, useEffect } from 'react'
import { useParams } from 'react-router';


import { getSingleProduct } from '../../../http/index';
import MyLoader from '../../../components/MyLoader/MyLoader';
import { useHistory } from 'react-router-dom';
import moment from 'moment';


export default function AdminSingleProduct() {

    const [ld, setld] = useState(true);
    const [md, setmd] = useState({ catergory: {}, tax: {}, prImages: [], parmentSeller: {}, aboutItem: [], productMap: [] });

    const { id } = useParams();

    const history = useHistory();

    function editProduct() {

        history.push(`/admin/dashboard/updateproduct/${id}`);
    }

    useEffect(() => {

        (async () => {
            try {

                const { data } = await getSingleProduct(id);

                //console.log(data);

                if (data.or) {

                    setmd(data.result);

                }
            } catch (error) {

                //console.log(error);

            }

            return setld(false);

        })()

    }, [id]);


    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>
                    <Container style={{ margin: "32px 0px", textTransform: 'capitalize' }}>
                        <Typography>
                            admin Single product list page
                        </Typography>
                    </Container>

                    <Container>
                        <Grid container justifyContent='space-between' alignItems='center' style={{ textTransform: 'capitalize', marginBottom: "24px" }}>
                            <Grid item>
                                <Typography variant='subtitle2'>
                                    Product id {md._id}
                                </Typography>
                            </Grid>
                            <Grid item>

                                <Button onClick={editProduct} color='secondary' variant='contained'>
                                    Edit Product
                                </Button>
                            </Grid>
                        </Grid>
                    </Container>

                    <Container style={{ textTransform: 'capitalize', marginTop: "32px" }}>
                        <Typography variant='h6' style={{ marginBottom: "16px" }}>
                            Product details
                        </Typography>

                        <Grid container alignItems='center' style={{ marginBottom: "24px" }}>

                            <Grid item xs container justifyContent='center'  >
                                <Grid item  >
                                    <img src={md.mainImage} alt={md.name} style={{ maxWidth: "100%", height: "200px" }}></img>
                                </Grid>

                            </Grid>
                            <Grid item xs>
                                <Typography>
                                    catergory  {md.catergory.name}
                                </Typography>
                                <Typography>
                                    name   {md.name}
                                </Typography>
                                <Typography>
                                    brand   {md.brand}
                                </Typography>
                                <Typography style={{ color: "blue" }}>
                                    status  {md.status ? "active" : "deactive"}
                                </Typography>

                                <Typography variant='subtitle2'>
                                    stock qauntity {md.inStock}
                                </Typography>

                                <Typography>
                                    des  {md.des}
                                </Typography>

                            </Grid>
                        </Grid>

                    </Container>

                    <Container >
                        <Typography variant='h6' style={{ marginBottom: "8px" }}>
                            Price details
                        </Typography>

                        <Typography variant='subtitle2'>
                            Tax rate on product {md.tax.rate} %
                        </Typography>

                        <Grid container justifyContent='space-between' alignItems='center' style={{ textTransform: 'capitalize' }}>

                            <Grid item >
                                <Typography style={{ marginBottom: "4px" }}>
                                    mrp  <i className="fa fa-inr" aria-hidden="true"></i> {md.mrp}
                                </Typography>
                                <Typography style={{ marginBottom: "4px" }}>
                                    spExGst  <i className="fa fa-inr" aria-hidden="true"></i> {md.spExGst}
                                </Typography>
                                <Typography style={{ marginBottom: "4px" }}>
                                    spInGst  <i className="fa fa-inr" aria-hidden="true"></i> {md.spInGst}
                                </Typography>

                                <Typography style={{ marginBottom: "4px" }}>
                                    discount {md.discount}
                                </Typography>

                            </Grid>
                            <Grid item >
                                <Typography>
                                    spExGstForS  <i className="fa fa-inr" aria-hidden="true"></i> {md.spExGstForS}
                                </Typography>
                                <Typography>
                                    spInGstForS  <i className="fa fa-inr" aria-hidden="true"></i> {md.spInGstForS}
                                </Typography>

                                <Typography>
                                    grossProfitForS  <i className="fa fa-inr" aria-hidden="true"></i> {md.grossProfitForS}
                                </Typography>
                                <Typography>
                                    netProfitForS  <i className="fa fa-inr" aria-hidden="true"></i> {md.netProfitForS}
                                </Typography>
                                <Typography>
                                    grossProfitMarginForS {" "} {md.grossProfitMarginForS} %
                                </Typography>
                                <Typography>
                                    netProfitMarginForS {" "} {md.netProfitMarginForS} %
                                </Typography>
                            </Grid>
                            <Grid item >
                                <Typography style={{ marginBottom: "8px" }}>
                                    deliveryCharge of product on website  <i className="fa fa-inr" aria-hidden="true"></i> {md.deliveryCharge}
                                </Typography>
                                <Typography style={{ marginBottom: "8px" }}>
                                    actualDeliveryCharge of product  <i className="fa fa-inr" aria-hidden="true"></i> {md.actualDeliveryCharge}
                                </Typography>

                                <Typography style={{ marginBottom: "8px" }}>
                                    createdAt {moment(md.createdAt).format("LLL")}
                                </Typography>

                                <Typography style={{ marginBottom: "8px" }}>
                                    parmentSeller name {md.parmentSeller.name}
                                </Typography>
                                <Typography style={{ marginBottom: "8px" }}>
                                    phone number  {md.parmentSeller.phone}
                                </Typography>
                                <Typography style={{ marginBottom: "8px" }}>
                                    address  {md.parmentSeller.address1}
                                </Typography>

                            </Grid>
                        </Grid>



                    </Container>
                    <Container style={{ marginTop: "24px" }}>
                        <Typography variant='h6' >
                            Images Of Product {md.prImages.length}
                        </Typography>


                        <Grid container spacing={3} style={{ marginTop: "24px" }}>

                            {
                                md.prImages.map((i) => {
                                    return (<Grid item xs key={i._id} container justifyContent='center' alignItems='center'>
                                        <Grid item >
                                            <img src={i.primage} alt={i.alt} style={{ maxWidth: "100%", height: "300px" }}>
                                            </img>
                                        </Grid>

                                    </Grid>)
                                })
                            }

                        </Grid>





                    </Container>

                    <Container style={{ textTransform: 'capitalize' }}>
                        <Typography variant='subtitle2' style={{margin:"16px 0px"}}>
                            About this Item
                        </Typography>
                        {
                            md.aboutItem.map((i) => {
                                return (<Typography key={i._id} style={{marginBottom:"4px"}}>{i.des}</Typography>)
                            })
                        }

                        <Typography variant='subtitle2' style={{margin:"16px 0px"}} >
                        Product Information
                        </Typography>
                        {
                            md.productMap.map((i) => {

                                return (
                                    <Box key={i._id}>
                                        <Typography variant='subtitle2' style={{marginBottom:"4px"}}>
                                            {i.mykey}:  {i.myvalue}
                                        </Typography>
                                    </Box>
                                )
                            })
                        }
                    </Container>


                </>
            }



        </div>
    )
}
