import { Container, Grid, Typography, Button } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import { useParams, useHistory } from 'react-router';


import { singleSupplySeller } from '../../../http/index';
import MyLoader from '../../../components/MyLoader/MyLoader';

export default function SupplierSingleMapPage() {

    const { id } = useParams();

    const history = useHistory();
    const [md, setmd] = useState({ productId: {} });

    const [ld, setLd] = useState(true);

    function EdSupply() {

        history.push(`/dashboard/map-update/${id}`);

    }

    useEffect(() => {

        (async () => {

            try {

                const { data } = await singleSupplySeller(id);

                //console.log(data);


                if (data.or) {

                    setmd(data.result);

                }
            } catch (error) {

                //console.log(error);
            }

            return setLd(false);

        })();

    }, [id]);

    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>
                    <Container>


                        <Grid container alignItems='center' spacing={2} style={{ margin: "32px 0px" }}  >

                            <Grid item xs sm >
                                <Typography>Single Supply Details Page {id} </Typography>
                            </Grid>

                            <Grid item xs={6} sm={3}>

                                <Button variant='contained' color='secondary' onClick={EdSupply}>
                                    Edit supply
                                </Button>
                            </Grid>
                        </Grid>

                    </Container>

                    <Container style={{ marginBottom: "24px", textTransform: 'capitalize' }}>

                        <Typography style={{ marginBottom: "8px" }} >
                            supply id {md._id} status {md.status ? <span style={{ color: "blue" }}>
                                active
                            </span> : <span style={{ color: "red" }}>
                                decative
                            </span>}
                        </Typography>

                        <Typography variant='subtitle2' style={{ marginBottom: "8px" }}>
                           Supply Price  <i className="fa fa-inr" aria-hidden="true"></i>  {md.price}
                        </Typography>

                        <Typography variant='subtitle2' style={{ marginBottom: "8px" }}>
                            Stock quatity of Supply {md.inStock}
                        </Typography>

                    </Container>

                    <Container>
                        <Grid container alignItems='center' style={{ textTransform: 'capitalize', marginTop: "24px" }}>

                            <Grid item xs={12} sm={3} container justifyContent='center'>
                                <Grid item >
                                    <img src={md.productId.mainImage} alt={md.productId.name} style={{ maxWidth: "100%", height: "300px" }}>
                                    </img>
                                </Grid>

                            </Grid>

                            <Grid item sm xs={12}>
                                <Typography style={{ marginBottom: "8px" }} >
                                    product  id {md.productId._id} {md.productId.status ? <span style={{ color: "blue" }}>
                                        active
                                    </span> : <span style={{ color: "red" }}>
                                        decative
                                    </span>}
                                </Typography>
                                <Typography style={{ marginBottom: "8px" }}>
                                    {md.productId.name}
                                </Typography>
                                <Typography variant='subtitle2' style={{ marginBottom: "8px" }}>
                                    MRP  <i className="fa fa-inr" aria-hidden="true"></i> {md.productId.mrp}
                                </Typography>
                                <Typography variant='subtitle2' style={{ marginBottom: "8px" }}>
                                    Selling Price  <i className="fa fa-inr" aria-hidden="true"></i> {md.productId.spInGst}
                                </Typography>
                            </Grid>

                        </Grid>
                    </Container>

                </>
            }



        </div>
    )
}
