import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';

import { sendProduct, getdataProductformforadd } from '../../../http/index';


export default function AdminProductForm() {

    const [st, setSt] = useState(false);

    const [fd, setfd] = useState({ subcatergory: [], tax: [] });

    const [name, setN] = useState('');
    let [subcater, setSubCater] = useState('');
    const [brand, setBrand] = useState('');
    const [image1, setIm1] = useState('');
    const [image2, setIm2] = useState('');
    const [image3, setIm3] = useState('');
    const [image4, setIm4] = useState('');
    const [image5, setIm5] = useState('');
    const [des, setD] = useState('');

    const [mrp, setMrp] = useState('');

    const [spSeller, setspSeller] = useState('');

    const [spCustomer, setspCustomer] = useState('');

    const [parmentSeller, setSellerPrId] = useState('');

    const [actualDeliveryCharge, setActualDelivery] = useState('');

    const [inStock, setInStock] = useState('');

    let [tax, setT] = useState('');

    const [prdes, setPrDes] = useState('');

    const [mydes, setMydes] = useState([]);

    const [mykey, setMykey] = useState('');

    const [myvalue, setMyvalue] = useState('');

    const [mymap, setMyMap] = useState([]);

    const [er, setEr] = useState(false);


    function add(e) {

        e.preventDefault();
        //console.log("button clicked");


        setMydes((pr) => {

            let ty = [];
            ty = pr;
            ty.push(prdes.toLowerCase());

            return ty;
        });

        return setPrDes('');

    }

    function addmap(e) {

        e.preventDefault();

        setMyMap((pr) => {
            let ty = [];
            ty = pr;
            ty.push({ mykey: mykey.toLowerCase(), myvalue: myvalue.toLowerCase() });

            return ty;
        });

        setMykey('');
        return setMyvalue('')
    }



    async function Submit(e) {

        e.preventDefault();
        // console.log("button clicked");

        //console.log(mydes);

        //console.log(mymap);



        if (!subcater) {
            subcater = fd.subcatergory[0];
        }

        if (!tax) {
            tax = fd.tax[0];
        }
        //console.log(subcater);
        //console.log(tax);


        // valdlaion logic

        if (!name || !brand || !mrp || !spSeller || !spCustomer || !tax || !subcater) {

            //console.log("valdtion error ");

            setEr(true);

            return;
        }

        const mydata = new FormData();

        if (image1) {
            mydata.append("image1", image1);
        }

        if (image2) {
            mydata.append("image2", image2);
        }

        if (image3) {
            mydata.append("image3", image3);
        }

        if (image4) {
            mydata.append("image4", image4);
        }

        if (image5) {
            mydata.append("image5", image5);
        }

        mydata.append("name", name.toLowerCase());
        mydata.append("des", des.toLowerCase());
        mydata.append("brand", brand.toLowerCase());
        mydata.append("catergory", subcater);
        mydata.append("tax", tax);
        mydata.append("mrp", mrp);
        mydata.append("sellerP", spSeller);
        mydata.append("customerP", spCustomer);
        mydata.append("parmentSeller", parmentSeller);
        mydata.append("actualDeliveryCharge", actualDeliveryCharge);
        mydata.append("inStock", parseInt(inStock));

        mydata.append("aboutItem", JSON.stringify(mydes))
        mydata.append("productMap", JSON.stringify(mymap));

        try {

            const { data } = await sendProduct(mydata);

            //console.log(data);

            if (data.or) {
                return setSt(true);
            }

        } catch (error) {

            //console.log(error);

            setEr(true);

        }


    }

    useEffect(() => {

        (async () => {
            try {

                const { data } = await getdataProductformforadd();

                //console.log(data);

                if (data.or) {

                    return setfd(data.result);

                }
            } catch (error) {

                //console.log(error);

            }

        })()

    }, []);



    return (
        <div>
            <p>AdminProductForm is here can added a product </p>

            {
                st ? <div>
                    <p>form is submit sucessfully </p>
                    <Link to='/admin/dashboard/productlist'>
                        go to products
                    </Link>
                </div> : <div>

                    {er && <p style={{ color: "red", marginBottom: "16px" }}>valation error </p>}
                    <form>
                        <label>
                            Enter Name of Product
                            <input type="text" value={name} onChange={(e) => {
                                setN(e.target.value);
                            }}></input>
                        </label><br></br>
                        <label>
                            select sub subergory
                            <select onChange={(e) => {
                                setSubCater(e.target.value);

                            }}>
                                {
                                    fd.subcatergory.map((i) => {
                                        return <option key={i}>{i}</option>
                                    })
                                }

                            </select>
                        </label><br></br>

                        <label>
                            tax class of product

                            <select onChange={(e) => {

                                setT(e.target.value);


                            }}>
                                {
                                    fd.tax.map((i) => {
                                        return <option key={i}>{i}</option>
                                    })
                                }
                            </select>
                        </label><br></br>


                        <label>
                            Enter brand of product
                            <input type="text" value={brand} onChange={(e) => {

                                setBrand(e.target.value);


                            }}></input>
                        </label><br></br>

                        <label>
                            Enter Image 1
                            <input type="file" onChange={(e) => {
                                setIm1(e.target.files[0]);

                            }}></input>
                        </label><br></br>

                        <label>
                            Enter Image 2
                            <input type="file" onChange={(e) => {
                                setIm2(e.target.files[0]);

                            }}></input>
                        </label><br></br>
                        <label>
                            Enter Image 3
                            <input type="file" onChange={(e) => {
                                setIm3(e.target.files[0]);

                            }}></input>
                        </label><br></br>
                        <label>
                            Enter Image 4
                            <input type="file" onChange={(e) => {
                                setIm4(e.target.files[0]);

                            }}></input>
                        </label><br></br>

                        <label>
                            Enter Image 5
                            <input type="file" onChange={(e) => {
                                setIm5(e.target.files[0]);

                            }}></input>
                        </label><br></br>

                        <label>
                            descpiriton of product
                            <input type="text" value={des} onChange={(e) => {
                                setD(e.target.value);


                            }}></input>
                        </label><br></br>


                        <label>
                            mrp of product
                            <input type="number" value={mrp} onChange={(e) => {

                                setMrp(e.target.value);


                            }}></input>
                        </label><br></br>


                        <label>
                            selling price  for seller
                            <input type="number" value={spSeller} onChange={(e) => {

                                setspSeller(e.target.value);



                            }}></input>
                        </label><br></br>

                        <label>
                            selling price  for customer
                            <input type="number" value={spCustomer} onChange={(e) => {

                                setspCustomer(e.target.value);


                            }}></input>
                        </label><br></br>

                        <label>
                            seller id of perment seller
                            <input type="text" value={parmentSeller} onChange={(e) => {

                                setSellerPrId(e.target.value);


                            }}></input>
                        </label><br></br>

                        <label>
                            delivery charge of product
                            <input type="number" value={actualDeliveryCharge} onChange={(e) => {

                                setActualDelivery(e.target.value);


                            }}></input>
                        </label><br></br>
                        <label>
                            set stock quantiy
                            <input type="number" value={inStock} onChange={(e) => {

                                setInStock(e.target.value);

                            }}></input>
                        </label><br></br>

                        {
                            mydes.map((i) => {
                                return (<p key={i}> {i}</p>)
                            })
                        }

                        <br></br>
                        <label>
                            Enter short des :<input type={'text'} value={prdes} onChange={(e) => {
                                setPrDes(e.target.value);
                            }}></input> <button onClick={add}>
                                Add
                            </button>
                        </label>
                        <br></br>

                        <br></br>
                        {
                            mymap.map((i) => {

                                return (<p key={i.mykey}>{i.mykey} {i.myvalue}</p>)
                            })
                        }


                        <br></br>
                        <label>
                            enter key and value <input value={mykey} onChange={(e) => {
                                setMykey(e.target.value);
                            }}></input> <input value={myvalue} onChange={(e) => {

                                setMyvalue(e.target.value);
                            }}></input>

                            <button onClick={addmap}>
                                add map
                            </button>
                        </label>
                        <br></br>
                        <br></br>                        <button onClick={Submit}>
                            Submit
                        </button>
                    </form>


                </div>
            }


        </div>
    )
}
