
import { Container, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Typography } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { othergetAllPayments } from '../../../http/index';
import MyLoader from '../../../components/MyLoader/MyLoader';

import moment from 'moment';

export default function AotherPayments() {
  const [md, setmd] = useState([{ sellerId: {}, orderId: {}, }]);


  const [ld, setld] = useState(true);

  useEffect(() => {

    (async () => {

      try {

        const { data } = await othergetAllPayments();

       // console.log(data);

        if (data.or) {

          setmd(data.result);


        }


      } catch (error) {

        //console.log(error);

      }

      return setld(false);

    })()
  }, []);

  return (
    <div>

      {
        ld ? <MyLoader></MyLoader> : <>
          <Container>
            <Typography variant='h6' style={{ textAlign: 'center', textTransform: 'capitalize', marginTop: "32px", marginBottom: "32px" }}>
              AotherPayments
            </Typography>
          </Container>
          <Container>
            <TableContainer>
              <Table>
                <TableHead style={{ textTransform: 'capitalize' }}>
                  <TableRow>
                    <TableCell>
                      payment id
                    </TableCell>
                    <TableCell>
                      name
                    </TableCell>
                    <TableCell>
                      amount of payment
                    </TableCell>
                    <TableCell>
                      amountof payment in bill
                    </TableCell>
                    <TableCell>
                      orderId
                    </TableCell>
                    <TableCell>
                      total orderAmount
                    </TableCell>
                    <TableCell>
                      orderStatus
                    </TableCell>
                    <TableCell>
                      paymentStatus
                    </TableCell>

                    <TableCell>
                      seller  name
                    </TableCell>
                    <TableCell>
                      createdAt
                    </TableCell>


                  </TableRow>
                </TableHead>
                <TableBody>

                  {
                    md.map((i) => {

                      return (<TableRow key={i._id}>
                        <TableCell>
                          <Link to={`/admin/dashboard/other/payment/single/${i._id}`}>
                            {i._id}
                          </Link>
                        </TableCell>
                        <TableCell>
                          {i.name}
                        </TableCell>
                        <TableCell style={{ fontWeight: "bold" }}>
                          <i className="fa fa-inr" aria-hidden="true"></i>  {i.amount}
                        </TableCell>
                        <TableCell style={{ fontWeight: "bold" }}>
                          <i className="fa fa-inr" aria-hidden="true"></i>  {i.amountofbill}
                        </TableCell>
                        <TableCell>
                          <Link to={`/admin/dashboard/other/orders/single/${i.orderId._id
                            }`}>
                            {i.orderId._id}
                          </Link>

                        </TableCell>
                        <TableCell style={{ fontWeight: "bold" }}>
                          <i className="fa fa-inr" aria-hidden="true"></i>  {
                            i.orderId.totalAmountofOrder
                          }
                        </TableCell>
                        <TableCell style={{ color: 'green' }}>
                          {
                            i.orderId.status
                          }
                        </TableCell>
                        <TableCell style={{ color: 'blue' }}>
                          {i.paymentStatus}
                        </TableCell>

                        <TableCell>

                          {
                            i.sellerId.name
                          }
                        </TableCell>

                        <TableCell>

                          {
                            moment(i.createdAt).format('LLL')
                          }
                        </TableCell>


                      </TableRow>)
                    })
                  }
                </TableBody>
              </Table>
            </TableContainer>
          </Container>



        </>
      }

    </div>
  )
}
