import { Container, Typography } from '@material-ui/core';
import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';

import { getSingleCatergory } from '../../../http/index';

export default function AdminSingleCatergory() {

  const [md, setmd] = useState({});

  const { id } = useParams();

  useEffect(() => {

    (async () => {

      try {
        const { data } = await getSingleCatergory(id);
        console.log(data);
        if (data.or) {
          return setmd(data.result);

        }
      } catch (error) {
        console.log(error);
      }


    })()

  }, [id]);


  return (
    <div>

      <Container>
        <Typography>
          single admin catergory {id}
        </Typography>
      </Container>

      <Container>

        <Typography>
          catergory id {md._id}
        </Typography>

        <Typography>
          name  {md.name}
        </Typography>
        <Typography>
          des {md.des}
        </Typography>
        <Typography>
          catergoryimage {md.catergoryimage}
        </Typography>
        <Typography>
          status {md.status}
        </Typography>
      </Container>
      <p>{md._id}</p>
    </div>
  )
}
