import React, { useEffect, useState } from 'react'

import { Button, Container, Grid, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Typography } from '@material-ui/core';

import { allPinCode } from '../../../http/index';
import { useHistory } from 'react-router-dom';

import MyLoader from '../../../components/MyLoader/MyLoader';

export default function AllPincode() {

    const [d, setD] = useState([]);

    const history = useHistory();
    const [ld, setld] = useState(true);



    useEffect(() => {

       // console.log("useEffect calling for pincode");


        (async () => {

            try {
                const { data } = await allPinCode();

               // console.log(data);

                if (data.or) {
                    setD(data.result);
                }
            } catch (error) {

               // console.log(error);

            }

            return setld(false);

        })();

    }, [])
    return (
        <div>


            {
                ld ? <MyLoader></MyLoader> : <>

                    <Container>
                        <Grid container justifyContent='space-between' style={{ marginTop: "32px", marginBottom: "32px" }}>

                            <Grid item>
                                <Typography variant='h6' style={{ textTransform: 'capitalize' }}>
                                    all pincode list for admin
                                </Typography>
                            </Grid>
                            <Grid item>
                                <Button onClick={() => {
                                    history.push('/admin/dashboard/pin/form');
                                }} variant='contained' color='primary'>
                                    Add Pincode
                                </Button>
                            </Grid>
                        </Grid>

                    </Container>

                    <Container>
                        <TableContainer>
                            <Table>
                                <TableHead>
                                    <TableRow>
                                        <TableCell>
                                            pin id
                                        </TableCell>
                                        <TableCell>
                                            name
                                        </TableCell>

                                        <TableCell>
                                            des
                                        </TableCell>

                                        <TableCell>
                                            totalP
                                        </TableCell>
                                        <TableCell>
                                            pincode
                                        </TableCell>
                                        <TableCell>
                                            Status
                                        </TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>

                                    {
                                        d.map((i) => {

                                            return (<TableRow key={i._id}>
                                                <TableCell>
                                                    TR  {i._id}

                                                </TableCell>
                                                <TableCell>
                                                    {i.name}
                                                </TableCell>
                                                <TableCell>
                                                    {i.des}
                                                </TableCell>
                                                <TableCell>
                                                    {i.totalP}
                                                </TableCell>

                                                <TableCell>
                                                    {i.pincode}
                                                </TableCell>
                                                <TableCell style={{color:"blue"}}>
                                                    {i.status ? "active" : "deactive"}
                                                </TableCell>
                                            </TableRow>)
                                        })
                                    }
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Container>
                </>
            }

        </div>
    )
}
