import { Container, Grid, Typography, Box, Button } from '@material-ui/core';
import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { useParams } from 'react-router-dom/cjs/react-router-dom.min';

import { getSingleAlert } from '../../../http/index';

import MyLoader from '../../../components/MyLoader/MyLoader';
import { useHistory } from 'react-router-dom';

export default function AdminSingleNotification() {

  const { id } = useParams();
  const [md, setmd] = useState({ supplyId: {}, productId: {}, sellerId: {} });
  const [ld, setld] = useState(true);

  const history = useHistory();
  function editAlert() {

    history.push(`/admin/dashboard/notification/edit/data/${id}`)

  }

  useEffect(() => {

    (async () => {

      try {
        const { data } = await getSingleAlert(id);

        //console.log(data);


        if (data.or) {
          setmd(data.result);

        }
      } catch (error) {

        //console.log(error);
        
      }

      return setld(false);

    })()

  }, [id]);
  return (
    <div>

      {
        ld ? <MyLoader></MyLoader> : <>
          <Container >
            <Typography variant='h6' style={{ margin: "32px 0px" }}>
              welcome to admin single notification
              alert id {id}
            </Typography>

          </Container>

          <Container style={{ textTransform: 'capitalize' }}>
            <Grid container justifyContent='space-between' alignItems='center'>
              <Grid item>
                <Typography variant='subtitle2'>alert id {md._id}</Typography>
                <Typography style={{ color: "blue" }}>alert status {md.status}</Typography>
              </Grid>
              <Grid item>

                <Button onClick={editAlert} color="secondary" variant='contained'>
                  Edit Alert
                </Button>
              </Grid>
            </Grid>
          </Container>

          <Container style={{ marginTop: "24px", textTransform: 'capitalize' }}>
            <Typography>
              seller id    {md.sellerId._id}
            </Typography>

            <Typography>
              name of seller    {md.sellerId.name}
            </Typography>
            <Typography>
              phone number     {md.sellerId.phone}
            </Typography>
            <Typography>
              address  {md.sellerId.address1}
            </Typography>
          </Container>

          <Container style={{ marginBottom: "24px", marginTop: "24px", textTransform: 'capitalize' }}>
            <Grid container justifyContent='space-between' alignItems='center'>
              <Grid item >
                <Box marginBottom={"8px"}>
                  <Link to={`/admin/dashboard/productlist/${md.productId._id}`}>

                    productId {md.productId._id}
                  </Link>
                </Box>

                <Typography>
                  product name   {md.productId.name}
                </Typography>
                <Typography style={{ color: "green" }}>
                  status of product    {md.productId.status ? "active" : "deactive"}
                </Typography>
                <Typography>
                  product mrp  <i className="fa fa-inr" aria-hidden="true"></i>   {md.productId.mrp}
                </Typography>
                <Typography>
                  customer price    <i className="fa fa-inr" aria-hidden="true"></i>  {md.productId.spInGst}
                </Typography>
                <Typography>
                  wholse price    <i className="fa fa-inr" aria-hidden="true"></i> {md.productId.spInGstForS}
                </Typography>
                <Typography>
                  stock quanity {md.productId.inStock}
                </Typography>
              </Grid>
              <Grid item >

                <Box marginBottom={"8px"}>
                  <Link to={`/dashboard/maplist/${md.supplyId._id}`}>
                    supply id  {md.supplyId._id}

                  </Link>
                </Box>

                <Typography style={{ color: "red" }}>
                  status of supply  {md.supplyId.status ? "active" : "deactive"}
                </Typography>
                <Typography>
                  price  <i className="fa fa-inr" aria-hidden="true"></i>  {md.supplyId.price}
                </Typography>
                <Typography>
                  stock quanity {md.supplyId.inStock}
                </Typography>
              </Grid>
            </Grid>



          </Container>

        </>
      }


    </div>
  )
}
