import React, { useState } from 'react';

import { regsiterCityState } from '../../../http/index';
import { Link } from 'react-router-dom';
export default function CityAreaServiceForm() {

    const [nameOfCity, setN] = useState('');
    const [stateOfCity, setSt] = useState('');
    const [countryOfCity, setC] = useState('');
    const [tierType, setTier] = useState('');
    const [pin, setP] = useState('');

    const [d, setD] = useState(false);
    const[er,setEr]=useState(false);
    async function submit(e) {

        e.preventDefault();

        //console.log("button clicked");

        // validtion error

        if (!nameOfCity || !stateOfCity || !countryOfCity || !tierType || !pin) {

           // console.log("validation error");
            setEr(true);
            return;
        }

        try {
            const { data } = await regsiterCityState({ nameOfCity, stateOfCity, countryOfCity, tierType, pin });
           
            //console.log(data);

            if (data.or) {

                return setD(true);
            }

        } catch (error) {

           // console.log(error);
            setEr(true);
        }

    }
    return (
        <div>
            <p>welcome to city state country form</p>
            {
                d ? <div>
                    <p>form is submit succesfflly </p>
                    <Link to={'/admin/dashboard/city/all'}>
                        go to city service
                    </Link>
                </div> : <form>
                <div>
                            {
                                er&&<p style={{color:'red'}}>
                                    all feilds are required
                                </p>
                            }
                        </div>
                    <label>
                        Enter name of city <input type="text" value={nameOfCity} onChange={(e) => {
                            setN(e.target.value);
                        }}></input>

                    </label><br></br>
                    <label>
                        Enter state of city <input type="text" value={stateOfCity} onChange={(e) => {
                            setSt(e.target.value);
                        }}></input>

                    </label><br></br>  <label>
                        Enter country of city <input type="text" value={countryOfCity} onChange={(e) => {
                            setC(e.target.value);
                        }}></input>

                    </label><br></br>  <label>
                        Enter tier type <input type="text" value={tierType} onChange={(e) => {
                            setTier(e.target.value);
                        }}></input>

                    </label><br></br>
                    <label>
                        Enter pincode <input type="number" value={pin} onChange={(e) => {
                            setP(e.target.value);
                        }}></input>

                    </label><br></br>

                    <button onClick={submit}>
                        submit
                    </button>

                </form>

            }

        </div>
    )
}
