import { Container, Typography } from '@material-ui/core';
import React, { useState, useEffect } from 'react';

import { useParams } from 'react-router-dom';


import { getSingleCityStateCountry } from '../../../http/index';

export default function AdminSingleCityArea() {

  const [md, setmd] = useState({});

  const { id } = useParams();

  useEffect(() => {

    (async () => {

      try {
        const { data } = await getSingleCityStateCountry(id);
        console.log(data);
        if (data.or) {
          return setmd(data.result);

        }
      } catch (error) {
        console.log(error);
      }

    })()

  }, [id]);

  return (
    <div>

      <Container>

        <Typography>
          admin single city CityArea
        </Typography>
      </Container>

      <Container>
        <Typography>city id {md._id}</Typography>
        <Typography>name of city {md.nameOfCity}</Typography>
        <Typography>state of city {md.stateOfCity}</Typography>
      </Container>


    </div>
  )
}
