import { Container, Typography } from '@material-ui/core';
import React, { useState, useEffect } from 'react';

import { useParams } from 'react-router-dom';

import { getSinglePin } from '../../../http/index';

export default function AdminSinglePin() {

  const [md, setmd] = useState({});

  const { id } = useParams();

  useEffect(() => {

    (async () => {
      try {
        const { data } = await getSinglePin(id);
       // console.log(data);

        if (data.or) {
          return setmd(data.result);

        }
      } catch (error) {
        //console.log(error);
      }

    })()

  }, [id]);

  return (
    <div>


      <Container>
        <Typography>
          admin single pin
        </Typography>
      </Container>

      <Container>
        <Typography>
          pincode id {md._id}
        </Typography>
        <Typography>
          pincode name  {md.name}
        </Typography>
        <Typography>
          pincode  {md.pincode}
        </Typography>
      </Container>


    </div>
  )
}
