import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';

import { getallBokingWorkforadmin } from '../../../http/index';

import { Container, Table, TableBody, TableCell, TableHead, TableRow, Typography } from '@material-ui/core';

import moment from 'moment';

import MyLoader from '../../../components/MyLoader/MyLoader';


export default function AdminBookingWork() {

    const [md, setmd] = useState([]);

    const [ld, setld] = useState(true);

    useEffect(() => {

        (async () => {

            try {

                const { data } = await getallBokingWorkforadmin();

                //console.log(data);

                if (data.or) {
                    setmd(data.result);

                }

            } catch (error) {

                //console.log(error);

            }

            return setld(false);

        })();

    }, [])


    return (
        <div>

{
    ld?<MyLoader></MyLoader>:<>
      <Container style={{ textTransform: 'capitalize', textAlign: 'center', margin: "32px 0px" }}>
                <Typography>Welcome to Booking Service List </Typography>
            </Container>


            <Table>
                <TableHead>
                    <TableRow>
                        <TableCell>id</TableCell>
                        <TableCell> workname</TableCell>
                        <TableCell>bookingStatus</TableCell>
                        <TableCell>CustomerName</TableCell>
                        <TableCell>phone Number</TableCell>
                        <TableCell> Address</TableCell>
                        <TableCell> CreatedAt</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {
                        md.map((i) => {
                            return (<TableRow key={i._id}>

                                <TableCell>
                                    <Link to={`/admin/dashboard/wrk/bookingwork/single/${i._id}`}>
                                        {i._id}
                                    </Link>
                                </TableCell>
                                <TableCell>{i.workname}</TableCell>
                                <TableCell style={{ color: "blue" }}>{i.bookingStatus}</TableCell>
                                <TableCell>{i.name}</TableCell>
                                <TableCell>{i.phoneNumber}</TableCell>
                                <TableCell> {i.address}</TableCell>
                                <TableCell>
                                    {moment(i.createdAt).format('LLL')}
                                </TableCell>
                            </TableRow>)
                        })
                    }

                </TableBody>
            </Table>

    </>
}

          
        </div>


    )
}
