import { Container, TextField, Typography, Box, Grid, Button } from '@material-ui/core';
import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';


import { registerSupply } from '../../../http/index';

export default function SupplierMapForm() {

    const [productId, setPrId] = useState('');
    const [price, setPrice] = useState('');
    const [inStock, setInstock] = useState('');
    const [d, setD] = useState(false);

    const history = useHistory();
    const [er, setEr] = useState(false);

    async function submit(e) {
        e.preventDefault();

       // console.log("button clicked");

        //valdiatiion error 


        if (!productId || !price || !inStock) {

           // console.log("validation error");
            setEr(true);
            return;

        }

        const condata = {
            productId,
            price: parseInt(price),
            inStock: parseInt(inStock),

        };



        try {
            const { data } = await registerSupply(condata);

            //console.log(data);

            if (data.or) {
                return setD(true);
            }
        } catch (error) {

           // console.log(error);
           setEr(true);
        }

    }
    return (
        <div>

            <Container style={{ textAlign: 'center', padding: '24px 0px',textTransform:'capitalize' }}>
                <Typography variant='h6' >
                    Welcome to supply form 
                </Typography>

            </Container>

            {
                d ? <Container style={{ textAlign: 'center' }}>
                    <Typography variant='' style={{ margin: "16px 0px", color: 'green' }}>
                        Map is Succesfully Added
                    </Typography>

                    <Box padding={"24px"}>
                        <Button color='primary' variant='contained' onClick={() => {
                            history.push('/dashboard/maplist');
                        }}>
                            Go Map List
                        </Button>
                    </Box>

                </Container> : <Container>
                    <form>

                        <Grid container justifyContent='center' style={{ paddingTop: "24px" }} >

                            <Grid item xs={12} sm={5}>

                                {er && <Box marginBottom={"16px"}>
                                    <Typography style={{ color: "red", textAlign: 'center', textTransform: 'capitalize' }}>
                                       Valdiatiion Error
                                    </Typography>
                                </Box>}

                                <Grid container spacing={2} alignItems='center' style={{ marginBottom: "24px" }}>
                                    <Grid item >
                                        <Typography style={{ textTransform: 'capitalize' }}>
                                            ProductId of product
                                        </Typography>
                                    </Grid>
                                    <Grid item xs >
                                        <TextField style={{ width: "100%" }} label='Enter Product id ' variant='outlined' size='small' value={productId} onChange={(e) => {
                                            setPrId(e.target.value);
                                        }}>
                                        </TextField>
                                    </Grid>

                                </Grid>
                                <Grid container spacing={2} alignItems='center' style={{ marginBottom: "24px" }}>
                                    <Grid item >
                                        <Typography style={{ textTransform: 'capitalize' }}>
                                            price of product
                                        </Typography>
                                    </Grid>
                                    <Grid item xs >
                                        <TextField style={{ width: "100%" }} label='Enter Price of Product ' variant='outlined' type={'number'} size='small' value={price} onChange={(e) => {
                                            setPrice(e.target.value);
                                        }}>
                                        </TextField>
                                    </Grid>

                                </Grid>

                                <Grid container spacing={2} alignItems='center' style={{ marginBottom: "24px" }}>
                                    <Grid item >
                                        <Typography style={{ textTransform: 'capitalize' }} >
                                            stock qutanity of product
                                        </Typography>
                                    </Grid>
                                    <Grid item xs >
                                        <TextField style={{ width: "100%" }} label='Enter Stock quantity ' variant='outlined' type={'number'} size='small' value={inStock} onChange={(e) => {
                                            setInstock(e.target.value);
                                        }}>
                                        </TextField>
                                    </Grid>

                                </Grid>


                                <Box marginTop={"34px"}>
                                    <Button onClick={submit} color='primary' variant='contained'>
                                        Submit
                                    </Button>
                                </Box>
                            </Grid>

                        </Grid>

                    </form>
                </Container>

            }




        </div>
    )
}
