import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';

import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';

import { Container, Grid, Typography } from '@material-ui/core';
import { useHistory } from 'react-router-dom';

import { logout } from '../../http/index';

import { setLogout } from '../../store/sellerSlice';
import { useDispatch, useSelector } from 'react-redux';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        flexGrow: 1,
    },
    offset: theme.mixins.toolbar,
    first: {

        [theme.breakpoints.down('xs')]: {
            display: "none",
        }
    },
    second: {
        display: 'none',
        [theme.breakpoints.down('xs')]: {
            display: "flex",
        }
    },

}));

export default function ButtonAppBar() {
    const classes = useStyles();
    const dispatch = useDispatch();

    const { isAuth, seller } = useSelector((state) => state.auth);

   
    async function logoutSeller() {

        //console.log("logout button clicekd");

        try {

            const { data } = await logout();

            //console.log(data);

            dispatch(setLogout(data.sellerDto));

        } catch (error) {
            //console.log(error);

        }

    }


    return (
        <div className={classes.root}>
            <Container>
                <AppBar >
                    <Toolbar>
                        <Grid container alignItems='center'>
                            <Grid item sm={2} xs={12} container>
                                <Grid item xs>
                                    <MyBtnLink to='/'>
                                        Archana Marketing
                                    </MyBtnLink>
                                </Grid>
                                <Grid item className={classes.second}>
                                    {
                                        (isAuth) && (seller.activated) && (seller.role === 'seller') && <MyMange></MyMange>

                                    }

                                    {
                                        (isAuth) && (seller.role === 'adminseller') && (seller.activated) && <>
                                            <MyMangeAdmin></MyMangeAdmin>
                                        </>
                                    }


                                    {
                                        (isAuth) && <MyAccount logout={logoutSeller}>
                                        </MyAccount>
                                    }
                                </Grid>


                            </Grid>
                            <Grid item sm xs={12} container alignItems='center' className={classes.first}>


                                <Grid item sm={7}>
                                    {
                                        (isAuth) && (seller.activated) && (seller.role === 'seller') && <>
                                            <MyBtnLink to='/dashboard'>
                                                dashboard
                                            </MyBtnLink>
                                            <MyBtnLink to='/dashboard/maplist'>
                                                Supply list
                                            </MyBtnLink>
                                            <MyBtnLink to='/dashboard/map-form'>
                                                Supply form
                                            </MyBtnLink>
                                            <MyBtnLink to='/dashboard/payment'>
                                                payments
                                            </MyBtnLink>
                                            <MyBtnLink to='/dashboard/return'>
                                                returns
                                            </MyBtnLink>
                                        </>
                                    }

                                    {
                                        (isAuth) && (seller.role === 'adminseller') && (seller.activated) && <>
                                            <MyMangeAdmin></MyMangeAdmin>
                                        </>
                                    }

                                </Grid>

                                <Grid item sm container alignItems='center'>

                                    <Grid item sm container alignItems='center' spacing={1} >
                                        <Grid item >
                                            {
                                                (isAuth) && (seller.activated) && <img alt={seller.name?seller.name:"as"} src={seller.shopimage?seller.shopimage:"ss"} style={{ height: "50px", borderRadius: "50%" }}>
                                                </img>
                                            }

                                        </Grid>
                                        <Grid item >
                                            {
                                                (isAuth) && (seller.activated) && <Typography style={{ textTransform: 'capitalize' }}>
                                                    {seller.name?seller.name:""}
                                                </Typography>
                                            }

                                        </Grid>


                                    </Grid>
                                    <Grid item >
                                        {
                                            (isAuth) ? <MyAccount logout={logoutSeller}></MyAccount> : <MyBtnLink to='/login'>
                                                Login
                                            </MyBtnLink>
                                        }


                                    </Grid>

                                </Grid>
                            </Grid>

                        </Grid>
                    </Toolbar>
                </AppBar>
            </Container>
            <div className={classes.offset} />
        </div>
    );
}



function MyBtnLink({ to, children }) {
    const history = useHistory();
    function s() {
        history.push(`${to}`);

    }
    return (
        <Button style={{ color: "white" }} onClick={s}>
            {children}
        </Button>
    )
}




function MyAccount({ logout }) {

    const [anchorEl, setAnchorEl] = React.useState(null);

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };
    const history = useHistory();
    function mProfile() {
        setAnchorEl(null);
        return history.push('/profile');

    }
    function handelLogout() {
        setAnchorEl(null);
        return logout();
    }

    return (
        <div>
            <Button style={{ color: "white", marginLeft: "8px" }} aria-controls="account" aria-haspopup="true" onClick={handleClick}>
                My Account
            </Button>
            <Menu
                id="account"
                anchorEl={anchorEl}
                keepMounted
                open={Boolean(anchorEl)}
                onClose={handleClose}
            >
                <MenuItem onClick={mProfile} component="button" style={{ marginBottom: "16px" }}>Profile</MenuItem>
                <MenuItem onClick={handelLogout} component="button" style={{ marginBottom: "16px" }}>Logout</MenuItem>
            </Menu>
        </div>)

}


function MyMange() {

    const [anchorEl, setAnchorEl] = React.useState(null);

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };
    const history = useHistory();

    function myDas1() {
        setAnchorEl(null);
        history.push('/dashboard');
    }
    function myDas2() {
        setAnchorEl(null);
        history.push('/dashboard/maplist');
    }
    function myDas3() {
        setAnchorEl(null);
        history.push('/dashboard/map-form');
    }
    function myDas4() {
        setAnchorEl(null);
        history.push('/dashboard/payment');
    }
    function myDas5() {
        setAnchorEl(null);
        history.push('/dashboard/return');
    }
    return (
        <div>
            <Button style={{ color: "white", marginLeft: "8px" }} aria-controls="simple-menu" aria-haspopup="true" onClick={handleClick}>
                Mangae
            </Button>
            <Menu
                id="simple-menu"
                anchorEl={anchorEl}
                keepMounted
                open={Boolean(anchorEl)}
                onClose={handleClose}
            >
                <MenuItem onClick={myDas1} component="button" style={{ marginBottom: "16px" }} >
                    Dashboard
                </MenuItem>
                <MenuItem onClick={myDas2} style={{ marginBottom: "16px", textTransform: 'capitalize' }} component="button"   >Supply list</MenuItem>
                <MenuItem onClick={myDas3} style={{ marginBottom: "16px", textTransform: 'capitalize' }} component="button"  >Add Supply</MenuItem>

                <MenuItem onClick={myDas4} style={{ marginBottom: "16px", textTransform: 'capitalize' }} component="button" >payments</MenuItem>
                <MenuItem onClick={myDas5} style={{ marginBottom: "16px", textTransform: 'capitalize' }} component="button"  >  returns</MenuItem>
            </Menu>
        </div>)

}



function MyMangeAdmin() {

    const [anchorEl, setAnchorEl] = React.useState(null);

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const history = useHistory();

    function myAdminDas1() {
        setAnchorEl(null);
        history.push('/admin/dashboard');
    }

    function myAdminDas2() {
        setAnchorEl(null);
        history.push('/admin/dashboard/prstatic');
    }
    function myAdminDas3() {
        setAnchorEl(null);
        history.push('/admin/dashboard/notification');
    }
    function myAdminDas4() {
        setAnchorEl(null);
        history.push('/admin/dashboard/other/orders');
    }

    function myAdminDas5() {
        setAnchorEl(null);
        history.push('/admin/dashboard/other/payment');
    }

    function myAdminDas6() {
        setAnchorEl(null);
        history.push('/admin/dashboard/other-work');
    }

    return (
        <div>
            <Button style={{ color: "white", marginLeft: "8px" }} aria-controls="admin" aria-haspopup="true" onClick={handleClick}>
                Mangae Admin
            </Button>
            <Menu
                id="admin"
                anchorEl={anchorEl}
                keepMounted
                open={Boolean(anchorEl)}
                onClose={handleClose}
            >
                <MenuItem onClick={myAdminDas1} style={{ marginBottom: "16px" }} component="button" >
                    Dashboard
                </MenuItem>
                <MenuItem onClick={myAdminDas2} style={{ marginBottom: "16px" }} component="button" >
                    Prstatic
                </MenuItem>
                <MenuItem onClick={myAdminDas3} style={{ marginBottom: "16px" }} component="button" >
                    Notification
                </MenuItem>
                <MenuItem onClick={myAdminDas4} style={{ marginBottom: "16px" }} component="button" >
                    orders
                </MenuItem>
                <MenuItem onClick={myAdminDas5} style={{ marginBottom: "16px" }} component="button" >
                    payments
                </MenuItem>
                <MenuItem onClick={myAdminDas6} style={{ marginBottom: "16px" }} component="button" >
                    Others
                </MenuItem>
            </Menu>
        </div>)

}

