import React, { useState } from 'react';
import { Link } from 'react-router-dom';


import { sendSubCatergory } from '../../../http/index';




export default function AdminSubCatergoryForm() {

    const [name, setName] = useState('');

    const [image, setImage] = useState('');
    const [des, setD] = useState('');
    const [pr, setPr] = useState('');
    const [er, setEr] = useState(false);

    const [com, setComp] = useState({
        status: false,
        data: {},
    });

    async function Submit(e) {

        e.preventDefault();
        //console.log("button clicked");

        // valdition 
        if (!name || !image || !des || !pr) {

            //console.log("valdtion error");
            setEr(true);
            return;
        }

        const mydata = new FormData();

        mydata.append("name", name.toLowerCase());
        mydata.append("des", des.toLowerCase());
        mydata.append("subcaterimage", image);
        mydata.append("parent", pr.toLowerCase());

        try {
            const { data } = await sendSubCatergory(mydata);

            //console.log(data);

            if (data.pr) {
                setComp({ status: true, data: data.d });

            }

        } catch (error) {

            //console.log(error);
            setEr(true);

        }
    }

    return (
        <div>
            <p>welcoem to Admin subcatergory from page js </p>
            {
                com.status ? <div>
                    <p style={{ color: "green" }}>subcatergory submmitted sucessfully</p>
                    <p>{com.data._id}</p>
                    <p>{com.data.name}</p>

                    <Link to='/admin/dashboard/sub-catergory'>
                        go to subcatergory</Link>
                </div> : <div>
                    <form>
                        <div>
                            {
                                er && <p style={{ color: 'red' }}>
                                    all feilds are required
                                </p>
                            }
                        </div>
                        <label>
                            Enter Name of Catergory
                            <input type="text" value={name} onChange={(e) => {
                                setName(e.target.value);


                            }}></input>
                        </label><br></br>
                        <label>
                            Enter des cription of catergory
                            <input type="text" value={des} onChange={(e) => {
                                setD(e.target.value);
                            }}></input>
                        </label><br></br>

                        <label>
                            Select Parent of Catergory

                            <input type={'text'} value={pr} onChange={(e) => {
                                setPr(e.target.value);

                            }}></input>
                        </label><br></br>

                        <label>
                            Choose Image
                            <input type="file" onChange={(e) => {
                                setImage(e.target.files[0]);

                            }}></input>
                        </label><br></br>

                        <button onClick={Submit}>
                            Submit
                        </button>
                    </form>
                </div>
            }

        </div>
    )
}
