import { Container, Grid, Paper, Typography, Box } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';

import { useLocation } from 'react-router-dom';
import MyLoader from '../../../components/MyLoader/MyLoader';
import { getSupplybysearchPrid } from '../../../http/index';
export default function SupplierMapSerach() {

    const { search } = useLocation();

    const [sl, setSl] = useState([{ productId: {} }]);

    const productId = search.split("=")[1];

    const [ld, setLd] = useState(true);

    useEffect(() => {

        (async () => {

            try {
                const { data } = await getSupplybysearchPrid({ productId: productId });

                //console.log(data);

                if (data.or) {
                    setSl(data.result);
                }
            } catch (error) {

                //console.log(error);
                

            }

            return setLd(false);

        })()
    }, [productId])


    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>
                    <Container>

                        <Typography variant='subtitle2' style={{ margin: "32px 0px", textTransform: 'capitalize', textAlign: 'center' }}>
                            welcome to supplier map search page list of supply   {productId}

                        </Typography>
                    </Container>

                    <Container>

                        {
                            sl.map((i) => {

                                return (
                                    <Paper key={i._id} style={{ textTransform: 'capitalize', padding: "8px", marginBottom: "24px" }}>
                                        <Grid container spacing={3} alignItems='center'>
                                            <Grid item >
                                                <Box marginTop={"8px"} marginBottom="8px">
                                                    <Link to={`/dashboard/maplist/${i._id}`}>
                                                        {i._id}
                                                    </Link>
                                                </Box>
                                                <Typography variant='subtitle2' style={{ marginBottom: "8px" }}>
                                                    supply Price  <i className="fa fa-inr" aria-hidden="true"></i>   {i.price}
                                                </Typography>
                                                <Typography variant='subtitle2' style={{ marginBottom: "8px" }}>
                                                    Stock quanity  {i.inStock}
                                                </Typography>
                                                <Typography style={{ marginBottom: "8px" }}>
                                                    Supply Status   {i.status ? <span style={{ color: "blue" }}>
                                                        active
                                                    </span> : <span style={{ color: "red" }}>
                                                        deactive
                                                    </span>}
                                                </Typography>
                                            </Grid>
                                            <Grid item container sm alignItems='center'>
                                                <Grid item xs={5} sm={5} container justifyContent='center'>

                                                    <Grid item >
                                                        <img src={i.productId.mainImage} alt={i.productId.name} style={{ maxWidth: "100%", height: "200px" }}>
                                                        </img>
                                                    </Grid>

                                                </Grid>
                                                <Grid item xs style={{ textTransform: 'capitalize' }}>
                                                    <Typography style={{ marginBottom: "8px" }}>
                                                        product id {i.productId._id}  and status   {i.productId.status ? <span style={{ color: "blue" }}>
                                                            active
                                                        </span> : <span style={{ color: "red" }}>
                                                            deactive
                                                        </span>}
                                                    </Typography>

                                                    <Typography style={{ marginBottom: "8px" }}>
                                                        name {i.productId.name}
                                                    </Typography>
                                                    <Typography variant='subtitle2' style={{ marginBottom: "8px" }} >
                                                        mrp  <i className="fa fa-inr" aria-hidden="true"></i> {i.productId.mrp}

                                                    </Typography>
                                                    <Typography variant='subtitle2' style={{ marginBottom: "8px" }} >

                                                        selling of  product  <i className="fa fa-inr" aria-hidden="true"></i>  {i.productId.spInGst}
                                                    </Typography>
                                                </Grid>




                                            </Grid>


                                        </Grid>
                                    </Paper>
                                )
                            })
                        }

                    </Container>

                </>
            }


        </div>
    )
}
