import React, { useEffect, useState } from 'react'

import { Link } from 'react-router-dom';
import { allSupplySeller } from '../../../http/index';

import { useHistory } from 'react-router-dom';
import { Container, Typography, Grid, TextField, Button, TableContainer, Table, TableHead, TableBody, TableRow, TableCell } from '@material-ui/core';

import MyLoader from '../../../components/MyLoader/MyLoader';

export default function SupplierMapListPage() {

    const [d, setD] = useState([{ productId: {} }]);
    const [sr, setSr] = useState('');

    const [ld, setLd] = useState(true);
    const history = useHistory();

    const [er, setEr] = useState(false);

    function submit() {

        //console.log("button clicked")

        if (sr.length < 20) {

            setEr(true);
            return;
        }


        history.push(`/dashboard/map/search/?q1=${sr}`);
    }


    useEffect(() => {


        //console.log("use effect calling for supply for seller ");

        (async () => {

            try {
                const { data } = await allSupplySeller();

                //console.log(data);

                if (data.or) {

                   setD(data.result);

                }

            } catch (error) {
                
                //console.log(error);

            }

            return setLd(false);

        })();

    }, [])


    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>
                    <Container style={{ marginTop: "34px" }} >

                        <Grid container spacing={3} alignItems='center' justifyContent='space-between'>
                            <Grid item xs sm>
                                <Typography style={{ textTransform: 'capitalize' }}>
                                    SupplierMapListPage in this we are seening list table
                                </Typography>
                            </Grid>
                            <Grid item xs={5} sm={3}>
                                <Button color="primary" variant='contained' onClick={() => {
                                    history.push('/dashboard/map-form');
                                }}>
                                    Add Supply
                                </Button>
                            </Grid>

                        </Grid>

                    </Container>

                    <Container>
                        {
                            er && <Typography style={{ color: 'red', margin: '16px 0px', textTransform: 'capitalize' }}>
                                inavalid product id
                            </Typography>
                        }

                        <Grid container spacing={3} alignItems='center'  >
                            <Grid item style={{ margin: "16px 0px" }}>
                                <TextField label='search by product id ' variant='outlined' size='small' value={sr} onChange={(e) => {
                                    setSr(e.target.value);
                                }}>

                                </TextField>
                            </Grid>
                            <Grid item sm >
                                <Button color='primary' variant='outlined' onClick={submit}>
                                    search
                                </Button>
                            </Grid>
                        </Grid>
                    </Container>

                    <Container style={{ marginTop: '24px' }}>

                        <Typography style={{ textAlign: 'center', marginBottom: "24px" }}>
                            List of All Supply
                        </Typography>
                        <TableContainer>
                            <Table>
                                <TableHead style={{ textTransform: 'capitalize' }}>

                                    <TableRow>
                                        <TableCell>
                                            _id
                                        </TableCell>
                                        <TableCell>
                                            productId
                                        </TableCell>
                                        <TableCell>
                                            name
                                        </TableCell>
                                        <TableCell>
                                            image
                                        </TableCell>
                                        <TableCell>
                                            product details
                                        </TableCell>
                                        <TableCell>
                                          Supply  Price 
                                        </TableCell>
                                        <TableCell>
                                         Supply   Stock
                                        </TableCell>
                                        <TableCell>
                                            status
                                        </TableCell>
                                    </TableRow>

                                </TableHead>
                                <TableBody>

                                    {
                                        d.map((i) => {
                                            return (
                                                <TableRow key={i._id}>

                                                    <TableCell>
                                                        <Link to={`/dashboard/maplist/${i._id}`}>{
                                                            i._id
                                                        }
                                                        </Link>

                                                    </TableCell>
                                                    <TableCell>
                                                        <Typography>
                                                            {i.productId._id}
                                                            {i.productId.status ? <span style={{ marginLeft: "10px", color: "blue" }}>
                                                                active
                                                            </span> : <span style={{ marginLeft: "10px", color: "red" }}>
                                                                deactive
                                                            </span>}
                                                        </Typography>


                                                    </TableCell>
                                                    <TableCell>
                                                        {i.productId.name}
                                                    </TableCell>
                                                    <TableCell>
                                                        <img src={i.productId.mainImage} alt={i.productId.name} style={{ maxWidth: "100%", height: "150px" }}>
                                                        </img>

                                                    </TableCell>
                                                    <TableCell>
                                                        <Typography variant='subtitle2'>
                                                            MRP  <i className="fa fa-inr" aria-hidden="true"></i>  {i.productId.mrp}
                                                        </Typography>
                                                        <Typography variant='subtitle2'>
                                                          Price <i className="fa fa-inr" aria-hidden="true"></i> {i.productId.spInGst}
                                                        </Typography>


                                                    </TableCell>
                                                    <TableCell>
                                                        <Typography variant='subtitle2'>
                                                        <i className="fa fa-inr" aria-hidden="true"></i>   {i.price}
                                                        </Typography>
                                                    </TableCell>
                                                    <TableCell>
                                                        <Typography variant='subtitle2'>
                                                            {i.inStock}
                                                        </Typography>

                                                    </TableCell>
                                                    <TableCell>
                                                        <Typography>
                                                            {i.status ? <span style={{ color: "blue" }}>
                                                                active
                                                            </span> : <span style={{ color: "red" }}>
                                                                deactive
                                                            </span>}
                                                        </Typography>

                                                    </TableCell>
                                                </TableRow>
                                            )
                                        })
                                    }

                                </TableBody>
                            </Table>

                        </TableContainer>
                    </Container>

                </>
            }

        </div>
    )
}
