import { Container, Typography } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';

import { otherSinglePayments } from '../../../http/index';
import MyLoader from '../../../components/MyLoader/MyLoader';
import moment from 'moment';

export default function AotherSinglePayments() {
  const [ld, setld] = useState(true);
  const [md, setmd] = useState({ orderId: {}, sellerId: {} });

  const { id } = useParams();

  useEffect(() => {

    (async () => {

      try {

        const { data } = await otherSinglePayments(id);

        //console.log(data);

        if (data.or) {

          setmd(data.result);
        }

      } catch (error) {

        //console.log(error);

      }

      return setld(false);

    })()
  }, [id]);

  return (
    <div>

      {
        ld ? <MyLoader></MyLoader> : <>
          <Container>
            <Typography variant='h6' style={{ margin: "32px 0px" }}>
              AotherSinglePayments by {id}
            </Typography>
          </Container>

          <Container style={{ textTransform: 'capitalize' }}>
            <Typography style={{ marginBottom: "8px" }}>
              Payment id {md._id}
            </Typography>
            <Typography style={{ marginBottom: "8px" }}>
              name of payment {md.name}
            </Typography>
            <Typography variant='subtitle2' style={{ marginBottom: "8px" }}>
              amount of payment  <i className="fa fa-inr" aria-hidden="true"></i> {md.amount}
            </Typography>
            <Typography variant='subtitle2' style={{ marginBottom: "8px" }}>
              amount of payment in bill  <i className="fa fa-inr" aria-hidden="true"></i>  {md.amountofbill}
            </Typography>
            <Typography style={{ marginBottom: "8px", color: "blue" }}>
              payment status {md.paymentStatus}
            </Typography>
            <Typography style={{ marginBottom: "8px", }}>
              date of creation of payment {moment(md.createdAt).format('LLL')}
            </Typography>
          </Container>

          <Container style={{ textTransform: 'capitalize' }}>

            <Typography style={{ marginBottom: "8px" }}>
              orderId  {md.orderId._id}
            </Typography>
            <Typography variant='subtitle2' style={{ marginBottom: "8px" }}>
              total amount of order <i className="fa fa-inr" aria-hidden="true"></i> {md.orderId.totalAmountofOrder}
            </Typography>
            <Typography style={{ marginBottom: "8px", color: "green" }}>
              order status {md.orderId.status}
            </Typography>
          </Container>
          <Container style={{ textTransform: 'capitalize' }}>

            <Typography style={{ marginBottom: "8px" }}>
              seller id  {md.sellerId._id}
            </Typography>
            <Typography  style={{ marginBottom: "8px" }}>
              seller name   {md.sellerId.name}
            </Typography>
            <Typography style={{ marginBottom: "8px",  }}>
              phone number {md.sellerId.phone}
            </Typography>
            <Typography style={{ marginBottom: "8px",  }}>
              address of shop   {md.sellerId.address1}
            </Typography>
          </Container>
        </>
      }


    </div>

  )
}
