import React, { useState } from 'react'
import { useHistory } from 'react-router';

import { otherRegisterReturn } from '../../../http/index';

export default function AotherReturnForm() {

    const history = useHistory();

    const [d, setD] = useState(false);

    const [name, setN] = useState('');

    const [amount, setAmount] = useState('');

    const [sellerId, setSellerId] = useState('');

    const [orderId, setOrderId] = useState('');

    const [retImage, setRetImage] = useState('');

    const [billImage, setBillImage] = useState('');
    const [er, setEr] = useState(false);

    async function submit(e) {
        e.preventDefault();

       // console.log("button clicked");

        //valdiation error

        if (!name || !amount || !sellerId || !orderId || !retImage || !billImage) {

            //console.log("valdition error ");
            setEr(true);
            return;
        }

        let formdata = new FormData();

        formdata.append("name", name.toLowerCase());

        formdata.append("amount", parseInt(amount));

        formdata.append("sellerId", sellerId);

        formdata.append("orderId", orderId);

        formdata.append("image1", retImage);

        formdata.append("image2", billImage);


        try {

            const { data } = await otherRegisterReturn(formdata);

          //  console.log(data);

            if (data.or) {

                return setD(true);
            }

        } catch (error) {

           // console.log(error);
            setEr(true);

        }

    }



    return (
        <div>
            <p>welcome to manger Returns form</p>
            {
                d ? <div>
                    <p>form is submitted succesffully </p>
                    <button onClick={() => {
                        history.push('/admin/dashboard/other/returns');
                    }}>
                        go Returns
                    </button>
                </div> : <div>
                    <form>
                        <div>
                            {
                                er && <p style={{ color: 'red' }}>
                                    all feilds are required
                                </p>
                            }
                        </div>
                        <label>
                            name of Returns <input type="text" value={name} onChange={(e) => {
                                setN(e.target.value);
                            }}></input>
                        </label><br>
                        </br>
                        <label>
                            amount of retuns  <input type="number" value={amount} onChange={(e) => {
                                setAmount(e.target.value);
                            }}></input>
                        </label><br>
                        </br>
                        <label>
                            seller Id  <input type="text" value={sellerId} onChange={(e) => {

                                setSellerId(e.target.value);
                            }}></input>
                        </label><br>
                        </br>
                        <label>
                            OrderId  <input type="text" value={orderId} onChange={(e) => {
                                setOrderId(e.target.value);
                            }}></input>
                        </label><br>
                        </br>

                        <label>
                            photo of retuns  <input type="file" onChange={(e) => {
                                setRetImage(e.target.files[0]);
                            }}></input>
                        </label><br>
                        </br>

                        <label>
                            purchase bill  <input type="file" onChange={(e) => {
                                setBillImage(e.target.files[0]);

                            }}></input>
                        </label><br>
                        </br>

                        <button onClick={submit}>
                            submit
                        </button>

                    </form>
                </div>
            }

        </div>
    )
}


