import { Button, Container, Grid, Typography, TextField, MenuItem } from '@material-ui/core';
import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';

import { useParams } from 'react-router-dom';


import { getdataforsupplyforedit, supplyeditupdate } from '../../../http/index';

import MyLoader from '../../../components/MyLoader/MyLoader';

export default function SupplierMapUpdate() {

    const [ld, setLd] = useState(true);

    const { id } = useParams();

    const [mydata, setmydata] = useState({ price: '', inStock: '', status: '' });

    let [price, setPrice] = useState('');

    let [inStock, setinstock] = useState('');

    let [st, setSt] = useState('');


    const [lt, setLt] = useState(false);

    const history = useHistory();

    async function submit(e) {

        e.preventDefault();

       // console.log("submit button clicked");

        let tmst;

        if (st === 'active') {
            tmst = true;
        } else {
            tmst = false;
        }


        let condata = {
            id: id,
            price: parseInt(price),
            inStock: parseInt(inStock),
            status: tmst,
        };


        try {
            const { data } = await supplyeditupdate(condata);

           // console.log(data);

            if (data.or) {

                return setLt(true);
            }
        } catch (error) {

            //console.log(error);
            

        }
    }

    useEffect(() => {


        (async () => {

            try {
                const { data } = await getdataforsupplyforedit(id);

                //console.log(data);

                const { result } = data;

                if (data.or) {

                    setmydata(result);
                    setPrice(result.price);
                    setinstock(result.inStock);
                    if (result.status) {
                        setSt("active");
                    } else {
                        setSt("deactive");
                    }

                }
            } catch (error) {

                //console.log(error);

            }

            return setLd(false);

        })();

    }, [id]);

    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>

                    <Container style={{ margin: "32px 0px", textAlign: 'center' }} >
                        <Typography variant='h6'>
                            SupplierMapUpdate form   {id}
                        </Typography>
                    </Container>

                    {
                        lt ? <Container>
                            <Grid container justifyContent='center'>
                                <Grid item >
                                    <Typography style={{ textTransform: 'capitalize', color: "green", marginBottom: "24px" }}>
                                        succesfully add
                                    </Typography>
                                    <Button variant='contained' color='primary' onClick={() => {
                                        history.push('/dashboard/maplist');
                                    }}>
                                        Go To Supply
                                    </Button>
                                </Grid>

                            </Grid>

                        </Container> : <>
                            <Container>

                                <Grid container justifyContent='center'>

                                    <Grid item xs={12} sm={4}>
                                        <Typography variant='button'> Status Of Supply {
                                            mydata.status ? <span style={{ color: "blue" }}>
                                                active
                                            </span> : <span style={{ color: "red" }}>
                                                deactive
                                            </span>
                                        }
                                        </Typography>
                                        <form style={{ marginTop: "24px", textTransform: 'capitalize' }}>
                                            <Grid container alignItems='center' spacing={2} style={{ marginBottom: "16px" }} >


                                                <Grid item >
                                                    <Typography >
                                                        price  <i className="fa fa-inr" aria-hidden="true"></i>
                                                    </Typography>
                                                </Grid>

                                                <Grid item xs>
                                                    <TextField type={'number'} value={price} variant='outlined' size='small' style={{ width: "100%" }} onChange={(e) => {
                                                        setPrice(e.target.value);
                                                    }}></TextField>
                                                </Grid>

                                            </Grid>
                                            <Grid container alignItems='center' spacing={2} style={{ marginBottom: "16px" }} >


                                                <Grid item >
                                                    <Typography >
                                                        stock quatinty
                                                    </Typography>
                                                </Grid>

                                                <Grid item xs>
                                                    <TextField type={'number'} value={inStock} variant='outlined' size='small' style={{ width: "100%" }} onChange={(e) => {
                                                        setinstock(e.target.value);
                                                    }} ></TextField>

                                                </Grid>



                                            </Grid>
                                            <Grid container alignItems='center' spacing={2} style={{ marginBottom: "16px" }} >


                                                <Grid item >
                                                    <Typography >select map status  </Typography>
                                                </Grid>

                                                <Grid item xs>
                                                    <TextField select style={{ width: "100%" }} variant='outlined' size='small' onChange={(e) => {
                                                        setSt(e.target.value);

                                                    }} value={st}>

                                                        <MenuItem value='active'>
                                                            active
                                                        </MenuItem>
                                                        <MenuItem value='deactive'>
                                                            deactive
                                                        </MenuItem>
                                                    </TextField>

                                                </Grid>

                                            </Grid>

                                            <Button onClick={submit} variant='contained' color='primary'>
                                                Update
                                            </Button>


                                        </form>
                                    </Grid>

                                </Grid>


                            </Container>
                        </>
                    }




                </>
            }


        </div>
    )
}
