import { Button, Container, Grid, MenuItem, TextField, Typography } from '@material-ui/core';
import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';

import { useParams } from 'react-router-dom';
import MyLoader from '../../../components/MyLoader/MyLoader';
import { getdataforeditofpayment, updatepayment } from '../../../http/index';

export default function SupplierPaymentUpdate() {

    const { id } = useParams();
    const [mydata, setmydata] = useState({ paymentStatus: "" });

    let [paymentStatus, setPaymentStatus] = useState('');

    const [my, setmy] = useState(false);

    const [er, setEr] = useState(false);

    const [ld, setLd] = useState(true);

    const history = useHistory();

    async function submit(e) {

        e.preventDefault();

        //console.log(paymentStatus);

        if (!paymentStatus) {
            setEr(true);
            return;
        }

        let condata = {
            id: id,
            paymentStatus,
        };

        //console.log(condata);


        try {
            const { data } = await updatepayment(condata);

           // console.log(data);


            if (data.or) {
                return setmy(true);

            }

        } catch (error) {

           // console.log(error);

        }



    }
    useEffect(() => {


        (async () => {

            try {
                const { data } = await getdataforeditofpayment(id);

               // console.log(data);

                if (data.or) {

                    setmydata(data.result);

                }
            } catch (error) {

                //console.log(error);


            }

            return setLd(false);
            
        })();

    }, [id]);



    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>
                    <Container>

                        <Typography variant='h6' style={{ textTransform: 'capitalize', margin: "32px 0px", textAlign: 'center' }}>
                            welcome to Supplier payment staus update {id}
                        </Typography>
                    </Container>




                    {
                        my ? <Container>

                            <Grid container justifyContent='center' style={{ marginTop: "32px", textTransform: 'capitalize' }}>

                                <Grid item >
                                    <Typography style={{ color: "green", marginBottom: "24px" }}>
                                        payment  update sucessfully
                                    </Typography>

                                    <Button onClick={() => {
                                        history.push('/dashboard/payment')
                                    }} variant='contained' color='primary'>
                                        Go To Payments
                                    </Button>
                                </Grid>
                            </Grid>

                        </Container> : <Container>

                            <Grid container justifyContent='center' style={{ marginTop: "16px", textTransform: 'capitalize' }}>

                                <Grid item sm={6} xs={12}>
                                    <Typography style={{ marginBottom: "8px" }}>
                                        this is payment id {mydata._id}
                                    </Typography>
                                    <Typography style={{ marginBottom: "8px" }}>
                                        name of payment {mydata.name}
                                    </Typography>
                                    <Typography style={{ marginBottom: "8px" }}>
                                        amount of payment  <i className="fa fa-inr" aria-hidden="true"></i> {mydata.amount}
                                    </Typography>
                                    <Typography style={{ marginBottom: "8px" }}>
                                        amount of payment in bill  <i className="fa fa-inr" aria-hidden="true"></i> {mydata.amountofbill}
                                    </Typography>
                                    <Typography style={{ marginBottom: "8px",color:"blue" }}>
                                        this is payemnt status {mydata.paymentStatus}
                                    </Typography>

                                    <form>

                                        <Typography style={{ marginBottom: "24px" }}>
                                            Update payment Status 
                                        </Typography>
                                        {
                                            er && <Typography style={{ marginBottom: "24px", color: 'red',textTransform:'capitalize' }}>
                                                payment status is  not selected
                                            </Typography>
                                        }
                                        <Grid container alignItems='center' style={{ marginBottom: "24px" }}>

                                            <Grid item>
                                                <Typography style={{ marginRight: "24px" }}>
                                                    Select Payment Status
                                                </Typography>

                                            </Grid>

                                            <Grid item xs>
                                                <TextField select onChange={(e) => {
                                                    setPaymentStatus(e.target.value);
                                                }} value={paymentStatus} size='small' variant='outlined' style={{ width: "100%" }}>

                                                    <MenuItem value='pending'>
                                                        pending
                                                    </MenuItem>
                                                    <MenuItem value='completed'>
                                                        completed
                                                    </MenuItem>
                                                </TextField>

                                            </Grid>

                                        </Grid>
                                        <Button onClick={submit} color='primary' variant='contained'>
                                            Update payment
                                        </Button>
                                    </form>
                                </Grid>
                            </Grid>



                        </Container>
                    }



                </>
            }



        </div >
    )
}
