import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';

import { othergeteditdataorder, otherupdateOrderstatus } from '../../../http/index';

import MyLoader from '../../../components/MyLoader/MyLoader';
import { Link } from 'react-router-dom';

import moment from 'moment';

export default function AotherEditOrder() {

  const [md, setmd] = useState({fullAddress:{},pincodeId:{}});

  const { id } = useParams();

  const [ld, setld] = useState(true);

  const [orStatus, setOrStatus] = useState('placed');


  const [st, setSt] = useState(false);

  useEffect(() => {

    (async () => {

      try {

        const { data } = await othergeteditdataorder(id);

        //console.log(data);


        if (data.or) {

          setmd(data.result);
        }

      } catch (error) {

        //console.log(error);

      }
      return setld(false);

    })()

  }, [id]);

  async function submit(e) {

    e.preventDefault();

   // console.log(orStatus);

    if (!orStatus) {

      //console.log("valaton error ");
      return;
    }

    const condata = {
      id: id,
      status: orStatus,
    };
    try {

      const { data } = await otherupdateOrderstatus(condata);

     // console.log(data);

      if (data.or) {

        return setSt(true);
      }


    } catch (error) {

     // console.log(error);

    }

  }

  return (
    <div>

      {
        ld ? <MyLoader></MyLoader> : <>
          <p>
            Edit OrderId{md._id}
          </p>


          <p style={{color:"blue"}}>order status {md.status}</p>

          <p>
            total amount of order  <i className="fa fa-inr" aria-hidden="true"></i> {md.totalAmountofOrder}
          </p>
          
         
          <p>
            createdAt {moment(md.createdAt).format("LLL")}
          </p>

          <p>
            deliverDate {moment(md.deliverDate).format('LLL')}
          </p>

          <p>
          delivery address  {md.address}
          </p>
          <p>
          {md.fullAddress.nameOfCity} {" "} {md.fullAddress.stateOfCity} {" "} {md.fullAddress.countryOfCity}
                  {" "} {md.pincodeId.pincode}
          </p>

          {
            st ? <div>
              <p>order status successfully updated </p>

              <Link to='/admin/dashboard/other/orders'>
                go to orders
              </Link>
            </div> : <div>
              <form>

                <label>
                  select order status
                  <select onChange={(e) => {
                    setOrStatus(e.target.value);
                  }}>
                    <option>placed</option>
                    <option>cancel</option>
                    <option>packaging</option>
                    <option>out of delivery</option>
                    <option>completed</option>

                  </select>
                </label>
                <br>
                </br>
                <button onClick={submit}>
                  Submit
                </button>
              </form>
            </div>
          }
        </>
      }


    </div>
  )
}
