import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom';
import { useParams } from 'react-router-dom';

import { getDataforeditofAlert, updateAlertStatus } from '../../../http/index';
import MyLoader from '../../../components/MyLoader/MyLoader';
import { Container } from '@material-ui/core';
export default function AdminNotificationUpdate() {

    let [st, setStatus] = useState("not seen");

    const [ld, setld] = useState(true);
    const { id } = useParams();

    const [md, setMd] = useState({ status: '' });

    const [ft, setFt] = useState(false);

    async function submit(e) {

        e.preventDefault();

        if (!st) {
            st = md.status;
        }

        //console.log(st);


        try {
            const { data } = await updateAlertStatus({ id: id, status: st });

            //console.log(data);

            if (data.or) {
                return setFt(true);

            }

        } catch (error) {

           // console.log(error);
           

        }
    }

    useEffect(() => {

        (async () => {

            try {
                const { data } = await getDataforeditofAlert(id);

                //console.log(data);

                if (data.or) {

                    setMd(data.result);

                }
            } catch (error) {
               // console.log(error);

            }
            return setld(false);
        })();
    }, [id]);

    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>


                    <Container style={{margin:"48px"}}>
                        <p>welcome to admin notification update </p>

                        <p>
                            alert id {md._id}
                        </p>

                        <p style={{color:"blue"}}> alert status {md.status}</p>

                        {
                            ft ? <div>
                                <p>update successfully done</p>

                                <Link to='/admin/dashboard/notification'>
                                    go to alerts
                                </Link>
                            </div> : <div>
                                <form>
                                    <label>
                                        change alert status : 
                                    </label>
                                    <select onChange={(e) => {
                                        setStatus(e.target.value);

                                    }}>
                                        <option>
                                            not seen
                                        </option>
                                        <option>
                                            seen
                                        </option>
                                    </select>
<br></br>
<br></br>

                                    <button onClick={submit}>
                                        submit
                                    </button>
                                </form>
                            </div>
                        }

                    </Container>

                </>
            }


        </div>
    )
}
