import { Container, Typography, TableContainer, Table, TableBody, TableHead, TableRow, TableCell } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom/cjs/react-router-dom.min';
import moment from 'moment';
import { othergetAllorder } from '../../../http/index';
import MyLoader from '../../../components/MyLoader/MyLoader';

export default function AotherOrder() {

  const [md, setmd] = useState([{pincodeId:{},fullAddress:{}}]);

  const [ld, setld] = useState(true);

  useEffect(() => {

    (async () => {

      try {

        const { data } = await othergetAllorder();

        //console.log(data);

        if (data.or) {

          setmd(data.result);
        }


      } catch (error) {

        //console.log(error);

      }
      return setld(false);
    })()
  }, []);


  return (
    <div>

      {
        ld ? <MyLoader></MyLoader> : <>
          <Container>
            <Typography variant='h6' style={{ textAlign: 'center', textTransform: 'capitalize', marginTop: "32px", marginBottom: "32px" }}>AotherOrder all  </Typography>
          </Container>

          <Container>
            <TableContainer>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell>
                      order id
                    </TableCell>
                    <TableCell>
                      totalamountof order
                    </TableCell>
                    <TableCell>
                    order  createdAt
                    </TableCell>
                    <TableCell>
                      name
                    </TableCell>
                    <TableCell>
                      phoneNumber
                    </TableCell>
                    <TableCell>
                      Address 
                    </TableCell>
                    <TableCell>
                    order   status
                    </TableCell>
                    <TableCell>
                    paymentType
                    </TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>

                  {
                    md.map((i) => {

                      return (<TableRow key={i._id}>
                        <TableCell>
                          <Link to={`/admin/dashboard/other/orders/single/${i._id}`}>
                            {i._id}
                          </Link>
                        </TableCell>
                        <TableCell>
                        <i className="fa fa-inr" aria-hidden="true"></i>  {i.totalAmountofOrder}
                        </TableCell>
                        <TableCell>

                          {
                            moment(i.createdAt).format("LLL")
                          }

                        </TableCell>
                        <TableCell>
                          {i.name}
                        </TableCell>
                        <TableCell>
                          {i.phoneNumber}
                        </TableCell>
                        <TableCell>
                          {i.address} {" ,"}{" "} {i.fullAddress.nameOfCity}{" "} {i.pincodeId.pincode}
                         </TableCell>
                        <TableCell style={{color:"blue"}}>
                          {i.status}
                        </TableCell>
                        <TableCell>
                        {i.paymentType}
                        </TableCell>
                      </TableRow>)
                    })
                  }
                </TableBody>
              </Table>
            </TableContainer>
          </Container>
        </>
      }


    </div>
  )
}
