import React, { useState, useEffect } from 'react';

import { useParams } from 'react-router';
import { useHistory } from 'react-router-dom';

import { singleReturnofSeller } from '../../../http/index';
import { Container, Grid, Typography, Button } from '@material-ui/core';

import MyLoader from '../../../components/MyLoader/MyLoader';

export default function SupplierSingleReturn() {

    const { id } = useParams();

    const [md, setmd] = useState({ orderId: {} });

    const [ld, setLd] = useState(true);

    const history = useHistory();

    function backtostep() {

        history.push(`/dashboard/return/update/${id}`);
    }



    useEffect(() => {

        (async () => {

            try {

                const { data } = await singleReturnofSeller(id);

                //console.log(data);


                if (data.or) {

                    setmd(data.result);

                }
            } catch (error) {

                //console.log(error);
                
            }

            return setLd(false);
        })();

    }, [id]);

    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>
                    <Container>


                        <Grid container style={{ margin: '32px 0px' }}>


                            <Grid item xs >
                                <Typography>Single Return  Details Page {id} </Typography>
                            </Grid>

                            <Grid item >

                                <Button onClick={backtostep} color='secondary' variant='contained'>
                                    Edit Return
                                </Button>
                            </Grid>
                        </Grid>

                    </Container>

                    <Container style={{ textTransform: 'capitalize' }}>
                        <Typography style={{ marginBottom: "8px" }}>
                            Return Id  {md._id}
                        </Typography>
                        <Typography style={{ marginBottom: "8px" }}>
                            name of return {md.name}
                        </Typography>
                        <Typography variant='h6' style={{ marginBottom: "8px" }}>
                            amount of return   <i className="fa fa-inr" aria-hidden="true"></i>  {md.amount}
                        </Typography>
                        <Typography style={{ marginBottom: "8px", color: "blue" }}>
                            status of Return {md.retStatus}
                        </Typography>
                        <Typography style={{ marginBottom: "8px" }}>
                            orderId {md.orderId._id}
                        </Typography>
                        <Typography variant='subtitle2' style={{ marginBottom: "8px" }}>
                           total  amount of Order  <i className="fa fa-inr" aria-hidden="true"></i> {md.orderId.totalAmountofOrder}
                        </Typography>
                        <Typography style={{ marginBottom: "8px", color: "green" }}>
                            order status {md.orderId.status}
                        </Typography>

                    </Container>

                    <Container >
                        <Typography variant='h6' style={{ marginTop: "32px", marginBottom: "32px" }}>
                            Photo And Bills
                        </Typography>
                        <Grid container alignItems='center'>

                            <Grid item xs={12} sm container>

                                <Grid item>
                                    <img src={md.retImage} alt='return ' style={{ maxWidth: "100%",height:"600px"  }}></img>
                                </Grid>

                            </Grid>
                            <Grid item xs={12} sm container>


                                <Grid item >
                                    <img src={md.billImage} alt='bill of  ' style={{ maxWidth: "100%" ,height:"600px" }}></img>
                                </Grid>
                            </Grid>
                        </Grid>

                    </Container>


                </>
            }



        </div>
    )
}
