import { Container, Typography, TableContainer, Table, TableHead, TableBody, TableRow, TableCell } from '@material-ui/core';
import moment from 'moment';
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';

import { allReturnsOfSeller } from '../../../http/index';

import MyLoader from '../../../components/MyLoader/MyLoader';

export default function SupplierReturnPage() {
    const [d, setD] = useState([{ orderId: {} }]);

    const [ld, setLd] = useState(true);


    useEffect(() => {

        //console.log("useffect calling for returns for sellers");

        (async () => {

            try {
                const { data } = await allReturnsOfSeller();

                //console.log(data);

                if (data.or) {

                    setD(data.result);

                }
            } catch (error) {

                //console.log(error);

            }

            return setLd(false);

        })();

    }, [])
    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>
                    <Container style={{ marginBottom: "48px" }}>
                        <Typography variant='h6' style={{ textAlign: 'center', margin: '24px 0px' }}>
                            Supplier Returns
                        </Typography>
                    </Container>

                    <Container>

                        <TableContainer>
                            <Table>
                                <TableHead style={{ textTransform: 'capitalize' }}>

                                    <TableRow>
                                        <TableCell>
                                            _id
                                        </TableCell>
                                        <TableCell>
                                            name
                                        </TableCell>
                                        <TableCell>
                                            amount
                                        </TableCell>
                                        <TableCell>
                                            OrderId

                                        </TableCell>
                                        <TableCell>
                                            Order total Amount

                                        </TableCell>
                                        <TableCell>
                                            order status

                                        </TableCell>
                                        <TableCell>
                                            Return Status
                                        </TableCell>
                                        <TableCell>
                                            date of Return
                                        </TableCell>

                                    </TableRow>

                                </TableHead>
                                <TableBody>

                                    {
                                        d.map((i) => {
                                            return (
                                                <TableRow key={i}>

                                                    <TableCell>
                                                        <Link to={`/dashboard/return/single/${i._id}`}>{
                                                            i._id
                                                        }
                                                        </Link>

                                                    </TableCell>
                                                    <TableCell>
                                                        {i.name}
                                                    </TableCell>
                                                    <TableCell>
                                                   
                                                        <i className="fa fa-inr" aria-hidden="true"></i> {i.amount}
                                                     
                                                    
                                                    </TableCell>
                                                    <TableCell>
                                                        {i.orderId._id}
                                                    </TableCell>
                                                    <TableCell>
                                                    <i className="fa fa-inr" aria-hidden="true"></i>  {i.orderId.totalAmountofOrder}
                                                    </TableCell>
                                                    <TableCell style={{color:"green" }}>
                                                        {i.orderId.status}
                                                    </TableCell>
                                                    <TableCell style={{ color: 'blue' }}>
                                                        {i.retStatus}
                                                    </TableCell>
                                                    <TableCell>



                                                        {
                                                            moment(i.createdAt).format("LLL")
                                                        }
                                                    </TableCell>
                                                </TableRow>
                                            )
                                        })
                                    }

                                </TableBody>
                            </Table>

                        </TableContainer>

                    </Container>


                </>
            }




        </div>
    )
}
