import React, { useState, useEffect } from 'react';

import { getallWorkerforadmin } from '../../../http/index';

import { Grid, Button, Container, Table, TableBody, TableCell, TableHead, TableRow, Typography } from '@material-ui/core';

import MyLoader from '../../../components/MyLoader/MyLoader';
import { useHistory } from 'react-router-dom';

export default function AdminWorkerList() {

  const [md, setmd] = useState([{ fullAddress: {}, pincodeId: {} }]);

  const [ld, setld] = useState(true);

  const history = useHistory();
  function tal() {

    history.push('/admin/dashboard/wrk/worker-form/add');

  }

  useEffect(() => {

    (async () => {

      try {

        const { data } = await getallWorkerforadmin();

        //console.log(data);

        if (data.or) {
          setmd(data.result);

        }

      } catch (error) {

        //console.log(error);

      }

      return setld(false);
    })();

  }, [])


  return (
    <div>

      {
        ld ? <MyLoader></MyLoader> : <>
 <Container style={{ textTransform: 'capitalize', textAlign: 'center', margin: "32px 0px" }}>
        <Typography>Welcome to AdminWorkerList </Typography>
      </Container>

      <Container style={{ textTransform: 'capitalize' }}>


        <Grid container justifyContent='flex-end' style={{ marginBottom: "32px" }}>
          <Grid item >
            <Button onClick={tal} color="primary" variant='contained'>
              Add Worker
            </Button>
          </Grid>
        </Grid>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>worker Id</TableCell>
              <TableCell>name</TableCell>
              <TableCell>phone</TableCell>
              <TableCell>address</TableCell>
              <TableCell> work</TableCell>
              <TableCell> isAvaible</TableCell>
              <TableCell> primage</TableCell>
              <TableCell>Status</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {
              md.map((i) => {
                return (<TableRow key={i._id}>

                  <TableCell>

                    {i._id}

                  </TableCell>
                  <TableCell>{i.name}</TableCell>
                  <TableCell >{i.phone}</TableCell>
                  <TableCell>{i.address} {i.fullAddress.nameOfCity} {" "}  {i.fullAddress.stateOfCity}{" "} {i.fullAddress.countryOfCity} {" "} {i.pincodeId.pincode}</TableCell>
                  <TableCell>{i.work}</TableCell>
                  <TableCell style={{ color: "blue" }}> {i.isAvaible}</TableCell>
                  <TableCell>
                    <img src={i.primage} alt={i.name} style={{ width: "150px" }}></img>
                  </TableCell>
                  <TableCell style={{ color: "blue" }}>
                    {i.status ? "active" : "deactive"}
                  </TableCell>
                </TableRow>)
              })
            }

          </TableBody>
        </Table>

      </Container>
        </>
      }

     
    </div>
  )
}
