import { Button, Container, Grid, Paper, TextField, Typography } from '@material-ui/core';
import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';

import ClassIcon from '@material-ui/icons/Class';
import { Link } from 'react-router-dom';

export default function SupplierDashboard() {

    const [sr, setSr] = useState('');

    const history = useHistory();

    const [er, setEr] = useState(false);

    function submit() {

        if (!sr) {
            setEr(true);
            return;
        }
        if (sr.length < 20) {

            setEr(true);
            return;
        }

        return history.push(`/dashboard/map/search/?q1=${sr}`);
    }

    return (
        <div>
            <Container style={{ padding: '24px 0px', textAlign: 'center' }}>
                <Typography style={{ textTransform: 'capitalize' }} variant='h6'>
                    welcome to SupplierDashboard page
                </Typography>
            </Container>

            <Container>
                {
                    er && <Typography style={{ color: 'red', margin: '16px 0px',textTransform:'capitalize', }}>
                        
                        invalid product id </Typography>
                }
                <Grid container spacing={3} alignItems='center'>

                    <Grid item >
                        <Typography style={{ textTransform: 'capitalize' }} >
                            serach by product id
                        </Typography>
                    </Grid>
                    <Grid item container sm spacing={1}>
                        <Grid item >
                            <TextField size='small' variant='outlined' value={sr} onChange={(e) => {
                                setSr(e.target.value);
                            }} >
                            </TextField>
                        </Grid>
                        <Grid item >
                            <Button onClick={submit} color='primary' variant='contained'>
                                Search
                            </Button>
                        </Grid>
                    </Grid>

                </Grid>
            </Container>



            <Container style={{ marginTop: '32px' }}>

                <Typography style={{ textAlign: 'center', marginBottom: '24px' }} variant='h6'>
                    OverView Of Dashboard
                </Typography>
                <Grid container spacing={3}>
                    <Grid item xs={12} sm >
                        <Paper style={{ padding: '8px', textAlign: 'center' }}>
                            <ClassIcon ></ClassIcon>
                            <Typography style={{ margin: '16px 0px' }}>
                                Total number of products {80}
                            </Typography>
                            <Link to='/dashboard/maplist' style={{ textDecoration: 'none', fontSize: '18px', margin: '16px 0px' }}>
                                view all
                            </Link>
                        </Paper>
                    </Grid>
                    <Grid item xs={12} sm>
                        <Paper style={{ padding: '8px', textAlign: 'center' }}>
                            <ClassIcon></ClassIcon>
                            <Typography style={{ margin: '16px 0px' }}>
                                Total number of payments {34}
                            </Typography>
                            <Link to='/dashboard/payment' style={{ textDecoration: 'none', fontSize: '18px', margin: '16px 0px' }}>
                                view all
                            </Link>
                        </Paper>
                    </Grid>
                    <Grid item xs={12} sm >
                        <Paper style={{ padding: '8px', textAlign: 'center' }}>
                            <ClassIcon></ClassIcon>
                            <Typography style={{ margin: '16px 0px' }}>
                                Total number of returns {20}
                            </Typography>
                            <Link to='/dashboard/return' style={{ textDecoration: 'none', fontSize: '18px', margin: '16px 0px' }}>
                                view all
                            </Link>
                        </Paper>
                    </Grid>
                </Grid>
            </Container>
        </div>
    )
}

